    ngOnInit() {
        let urlParams;
        this.activatedRoute.params.subscribe(params => {
            urlParams = params;
        });
        // console.log(urlParams);
        if (urlParams && JSON.stringify(urlParams) != '{}') {
            if (urlParams.flag != '1') {
                this.taskArrangementQuery2();
            }
        }
    }

    navChange(i) {
        this['showIndex0'] = i;
        console.log(this.showIndex0);
        if (i == 1) {
            this.taskArrangementQuery2();
        } else if (i == 2) {
            this.departmentProportionQuery(-1, 1);
        }
    }

    outValue(event) {
        let objTemp;
        if (event.index == 1) {
            objTemp = this.customerPerformanceService.returnTime(event.queryCondition, event.index);
        } else if (event.index == 2) {
            objTemp = event.queryCondition;
        }
        this.startDate = objTemp.startDate;
        this.endDate = objTemp.endDate;
        console.log('startDate:', this.startDate);
        console.log('endDate:', this.endDate);
        this.taskArrangementQuery2();
        // this.departmentProportionQuery(-1, 1);
    }

    returnParams(ind, index) {
        this[`showIndex${ind}`] = index;
        if (index == 1) {
            this.dmFlag = 'D';
        } else {
            this.dmFlag = 'M';
        }
    }

    echoParams(ind, flag) {
        let index;
        if (flag == 'D') {
            index = 1;
        } else {
            index = 2;
        }
        this[`showIndex${ind}`] = index;
    }

    routerLink(ind, index) {
        let routerLinkUrl;
        if (ind == 1) {
            switch (index) {
                case 1: routerLinkUrl = '/pages/tzb/custom/performance-analysis/deposit/growthGuide'; break;
                case 2: routerLinkUrl = '/pages/tzb/custom/performance-analysis/deposit/increasementalTendency'; break;
                case 3: routerLinkUrl = '/pages/tzb/custom/performance-analysis/deposit/savingNature'; break;
                case 4: routerLinkUrl = '/pages/tzb/custom/performance-analysis/deposit/productCategory'; break;
                case 5: routerLinkUrl = '/pages/tzb/custom/performance-analysis/deposit/tendcyChange'; break;
            }
        } else if (ind == 2) {
            switch (index) {
                case 1: routerLinkUrl = '/pages/tzb/custom/performance-analysis/loan/growthGuide'; break;
                case 2: routerLinkUrl = '/pages/tzb/custom/performance-analysis/loan/increasementalTendency'; break;
                case 3: routerLinkUrl = '/pages/tzb/custom/performance-analysis/loan/industryInvestment'; break;
                case 4: routerLinkUrl = '/pages/tzb/custom/performance-analysis/loan/productCategory'; break;
                case 5: routerLinkUrl = '/pages/tzb/custom/performance-analysis/loan/tendcyChange'; break;
            }
        } else if (ind == 3) {
            switch (index) {
                case 1: routerLinkUrl = '/pages/tzb/custom/performance-analysis/ftp/ftpBeforeDiscount'; break;
                case 2: routerLinkUrl = '/pages/tzb/custom/performance-analysis/ftp/ftpAfterDiscount'; break;
            }
        } else {
            switch (index) {
                case 1: routerLinkUrl = '/pages/tzb/custom/performance-analysis/badness/tendcyChange'; break;
                case 2: routerLinkUrl = '/pages/tzb/custom/performance-analysis/badness/propertyDistribution'; break;
            }
        }
        this.router.navigate([routerLinkUrl, { Property3: false, condition: 'recentOneMonth', flag: 1 }]);
    }

