import { Component, OnInit } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CustomerPerformanceHttpService } from 'app/pages/tzb/custom/http/customer-performance/customer-performance.http.service';
import { Message } from 'primeng/primeng';
import { CustomerPerformanceService } from '../../service/customer-performance.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
    selector: 'deposit-productCategory',
    templateUrl: './deposit-productCategory.component.html',
    styleUrls: ['./deposit-productCategory.component.scss'],
    providers: [Commfunc]
})

export class DepositProductCategoryComponent implements OnInit {

    option1: any;
    option2: any;
    option3: any;
    option4: any;
    option5: any;
    option6: any;
    commonHeader: any;
    msgs: Message[] = [];
    showIndex1: number = 1;
    showIndex2: number = 1;
    dmFlag: string;
    dwFlag: string = '1';
    gearList: any[] = [
        { label: '0以上', value: '1' },
        { label: '大于等于1000', value: '2' },
        { label: '大于等于5000', value: '3' },
        { label: '大于等于10000', value: '4' },
        { label: '大于等于20000', value: '5' },
        { label: '大于等于50000', value: '6' },
    ];
    startDate: any = this.commfunc.transDateN(new Date().setTime(new Date().getTime() - 1000 * 60 * 60 * 24 * 30));
    endDate: any = this.commfunc.transDateN(new Date());
    pTooltip1: string = '截止到所选时间的最后一日，我的存款余额在各产品的余额与占比';
    pTooltip2: string = '截止到所选时间的最后一日，部门合计存款余额在各产品余额与占比';
    pTooltip3: string = '按日展示我的各产品每日存款余额的变化趋势，按月展示自然月内各产品存款日均变化趋势';
    pTooltip4: string = `截止到所选时间的最后一日，我的持有存款客户数在各产品的客户数与占比\n注：允许单个客户可同时持有多个产品，故合计占比有可能超过100%`;
    pTooltip5: string = `截止到所选时间的最后一日，部门合计持有存款客户数在各产品的客户数与占比\n注：允许单个客户可同时持有多个产品，故合计占比有可能超过100%`;
    pTooltip6: string = '按日展示我的各产品每日持有存款客户数的变化趋势，按月展示各产品在自然月持有存款产品的客户数变化趋势';
    productBigId: string;   // 产品大类
    productSmallId: string;  // 产品小类
    moneyGears: string = '1';  // 档位
    bigProductList: any[] = [   // 产品大类
        { label: '请选择', value: '' },
        { label: '金添储蓄', value: 'L2011' },
        { label: '个人活期', value: 'L2012' },
        { label: '单位活期存款', value: 'L2013' },
        { label: '单位定期存款', value: 'L2014' },
        { label: '保证金存款', value: 'L2015' },
        { label: '其他存款', value: 'L2016' }
    ];

    smallProductList: any;
    smallProductList1: any;
    smallProductList2: any;
    smallProductList3: any;
    smallProductList4: any;
    smallProductList5: any;
    smallProductList6: any;

    moneyGearsOption: any = [  // 档位
        { label: '请选择', value: '' }
    ]

    code: any;
    urlParams: any;

    constructor(
        public commfunc: Commfunc,
        public customerPerformanceHttpService: CustomerPerformanceHttpService,
        public customerPerformanceService: CustomerPerformanceService,
        public router: Router,
        public activatedRoute: ActivatedRoute,
    ) {
        this.commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.codeVal();
        this.bigProductList = this.code['depositProdBigType'];
        this.bigProductList.unshift({ label: '请选择', value: '' });
        this.smallProductList1 = this.code['depositProdSmallType1'];
        this.smallProductList1.unshift({ label: '总计', value: 'total' });
        this.smallProductList1.unshift({ label: '请选择', value: '' });
        this.smallProductList2 = this.code['depositProdSmallType2'];
        this.smallProductList3 = this.code['depositProdSmallType3'];
        this.smallProductList4 = this.code['depositProdSmallType4'];
        this.smallProductList5 = this.code['depositProdSmallType5'];
        this.smallProductList6 = this.code['depositProdSmallType6'];
        //初值
        this.option1 = this.customerPerformanceService.returnOption2({ title: '我的各产品金额占比', name: '行业占比', data: [0] });
        this.option2 = this.customerPerformanceService.returnOption2({ title: '部门各产品金额占比', name: '行业占比', data: [0] });
        this.option3 = this.customerPerformanceService.returnOption({ title: '各产品变化趋势', legend: [], xAxisData: [], yAxisName: '金额(万元)', series: [] });
        this.option4 = this.customerPerformanceService.returnOption2({ title: '我的各产品客户数占比', name: '行业占比', data: [0] });
        this.option5 = this.customerPerformanceService.returnOption2({ title: '部门各产品客户数占比', name: '行业占比', data: [0] });
        this.option6 = this.customerPerformanceService.returnOption({ title: '各产品变化趋势', legend: [], xAxisData: [], yAxisName: '客户数', series: [] });
    }

    ngOnInit() {
        this.activatedRoute.params.subscribe(params => {
            this.urlParams = params;
        });
        // console.log(urlParams);
        if (this.urlParams && JSON.stringify(this.urlParams) != '{}') {
            if (this.urlParams.flag != '1') {
                this.depositProductQuery();         // 产品大类查询---饼图
                this.depositProductTrendQuery(0, 1);
            }
        }
        // this.loansProductTrendQuery(0, 0);
    }

    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    // 产品大类 切换
    changeBigType() {
        console.log(this.productBigId);
        switch (this.productBigId) {
            case 'L2011':
                this.smallProductList = this.smallProductList1;
                break;
            case 'L2012':
                this.smallProductList2.unshift({ label: '总计', value: 'total' });
                this.smallProductList2.unshift({ label: '请选择', value: '' });
                this.smallProductList = this.smallProductList2;
                break;
            case 'L2013':
                this.smallProductList3.unshift({ label: '总计', value: 'total' });
                this.smallProductList3.unshift({ label: '请选择', value: '' });
                this.smallProductList = this.smallProductList3;
                break;
            case 'L2014':
                this.smallProductList4.unshift({ label: '总计', value: 'total' });
                this.smallProductList4.unshift({ label: '请选择', value: '' });
                this.smallProductList = this.smallProductList4;
                break;
            case 'L2015':
                this.smallProductList5.unshift({ label: '总计', value: 'total' });
                this.smallProductList5.unshift({ label: '请选择', value: '' });
                this.smallProductList = this.smallProductList5;
                break;
            case 'L2016':
                this.smallProductList6.unshift({ label: '总计', value: 'total' });
                this.smallProductList6.unshift({ label: '请选择', value: '' });
                this.smallProductList = this.smallProductList6;
                break;
        }
    }

    outValue(event) {
        let objTemp;
        if (event.index == 1) {
            objTemp = this.customerPerformanceService.returnTime(event.queryCondition, event.index);
        } else if (event.index == 2) {
            objTemp = event.queryCondition;
        }
        this.startDate = objTemp.startDate;
        this.endDate = objTemp.endDate;
        console.log('startDate:', this.startDate);
        console.log('endDate:', this.endDate);
        this.depositProductQuery();         // 产品大类查询---饼图
        this.depositProductTrendQuery(0, 1);
    }

    returnParams(ind, index) {
        this[`showIndex${ind}`] = index;
        if (index == 1) {
            this.dmFlag = 'D';
        } else {
            this.dmFlag = 'M';
        }
    }

    echoParams(ind, flag) {
        let index;
        if (flag == 'D') {
            index = 1;
        } else {
            index = 2;
        }
        this[`showIndex${ind}`] = index;
    }


    // 跳转产品小类
    toSmalProduct() {
        if (this.productSmallId) {
            this.router.navigate(['/pages/tzb/custom/performance-analysis/deposit/smallClassProduct', { Property3: this.urlParams.Property3, condition: this.urlParams.condition, flag: this.urlParams.flag, productId: this.productBigId, smallClassProductId: this.productSmallId }]);
        }
    }

    depositProductQuery() {//存款产品个人占比查询
        let params = {
            tellerId: this.commonHeader.userId,
            dwFlag: this.dwFlag,
            startDate: this.startDate,
            endDate: this.endDate,
        }
        this.customerPerformanceHttpService.depositProductQuery(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let data1 = [];     // 我的各产品金额占比---饼图
                let data2 = [];     //  部门各产品金额占比---饼图   
                let xData1 = [];    // 我的各产品客户占比---柱状图
                let yData1 = [];
                let xData2 = [];    //  部门各产品客户占比---柱状图
                let yData2 = [];
                if (data.myAmtList && data.myAmtList.length != 0) {
                    data.myAmtList.forEach(element => {
                        this.bigProductList.forEach(element2 => {
                            if (element.productId == element2.value) {
                                let objTemp = {}
                                objTemp['name'] = element2.label;
                                objTemp['value'] = element.myAmt;
                                data1.push(objTemp);
                            }
                        });
                    });
                }
                if (data.depAmtList && data.depAmtList.length != 0) {
                    data.depAmtList.forEach(element => {
                        this.bigProductList.forEach(element2 => {
                            if (element.productId == element2.value) {
                                let objTemp = {}
                                objTemp['name'] = element2.label;
                                objTemp['value'] = element.depAmt;
                                data2.push(objTemp);
                            }
                        });
                    });
                }
                if (data.myCustList && data.myCustList.length != 0) {
                    this.bigProductList.forEach(element => {
                        data.myCustList.forEach(element2 => {
                            if (element2.productId == element.value) {
                                xData1.push(element.label);
                            }
                        });
                    });
                    data.myCustList.forEach(element => {
                        yData1.push(element.myCust);
                    });

                }
                if (data.depCustList && data.depCustList.length != 0) {
                    this.bigProductList.forEach(element => {
                        data.depCustList.forEach(element2 => {
                            if (element2.productId == element.value) {
                                xData2.push(element.label);
                            }
                        });
                    });
                    data.depCustList.forEach(element => {
                        yData2.push(element.depCust);
                    });

                }
                this.option1 = {
                    title: {
                        text: '我的各产品金额占比',
                        left: 'center',
                        y: '10px',
                        textStyle: {
                            fontWeight: '500',
                            fontSize: '16'
                        }
                    },
                    series: [
                        {
                            name: '行业占比',
                            data: data1,
                            type: 'pie',
                            radius: ['50%', '70%'],
                            label: {
                                normal: {
                                    formatter: '{b} {c} , {d}%'
                                }
                            }
                        }
                    ]
                }
                this.option2 = {
                    title: {
                        text: '部门各产品金额占比',
                        left: 'center',
                        y: '10px',
                        textStyle: {
                            fontWeight: '500',
                            fontSize: '16'
                        }
                    },
                    series: [
                        {
                            name: '行业占比',
                            data: data2,
                            type: 'pie',
                            radius: ['50%', '70%'],
                            label: {
                                normal: {
                                    formatter: '{b} {c} , {d}%'
                                }
                            }
                        }
                    ]
                }

                this.option4 = {
                    title: {
                        text: '我的各产品客户数占比',
                        left: 'center',
                        y: '10px',
                        textStyle: {
                            fontWeight: '500',
                            fontSize: '16'
                        }
                    },
                    // legend: {
                    //     data: [{ name: '客户数变化' }, { name: '占比变化' }],
                    //     top: 'bottom',
                    //     itemGap: 20,
                    //     icon: 'line'
                    // },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'cross'
                        }
                    },
                    grid: {
                        width: '80%',
                        height: '75%',
                        left: '10%',
                        bottom: '10%',
                        containLabel: true
                    },
                    xAxis: {
                        type: 'category',
                        data: xData1,
                        axisLabel: {
                            rotate: 30
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            show: false
                        }
                    },
                    yAxis: [
                        {
                            name: '客户数',
                            type: 'value',
                            nameGap: 30,
                            scale: true,
                            splitArea: {
                                show: true
                            },
                            axisTick: {
                                show: false
                            },
                            axisLine: {
                                show: false
                            }
                        }
                    ],
                    series: [
                        {
                            name: '客户数变化',
                            data: yData1,
                            type: 'bar',
                            // yAxisIndex: 0,
                            barWidth: 30,
                            itemStyle: {
                                normal: {
                                    label: {
                                        show: true,
                                        position: 'top'
                                    }
                                }
                            }
                        }
                    ]
                }

                this.option5 = {
                    title: {
                        text: '部门各产品客户数占比',
                        left: 'center',
                        y: '10px',
                        textStyle: {
                            fontWeight: '500',
                            fontSize: '16'
                        }
                    },
                    // legend: {
                    //     data: [{ name: '客户数变化' }, { name: '占比变化' }],
                    //     top: 'bottom',
                    //     itemGap: 20,
                    //     icon: 'line'
                    // },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'cross'
                        }
                    },
                    grid: {
                        width: '80%',
                        height: '75%',
                        left: '10%',
                        bottom: '10%',
                        containLabel: true
                    },
                    xAxis: {
                        type: 'category',
                        data: xData2,
                        axisLabel: {
                            rotate: 30
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            show: false
                        }
                    },
                    yAxis: [
                        {
                            name: '客户数',
                            type: 'value',
                            nameGap: 30,
                            scale: true,
                            splitArea: {
                                show: true
                            },
                            axisTick: {
                                show: false
                            },
                            axisLine: {
                                show: false
                            }
                        }
                    ],
                    series: [
                        {
                            name: '客户数变化',
                            data: yData2,
                            type: 'bar',
                            // yAxisIndex: 0,
                            barWidth: 30,
                            itemStyle: {
                                normal: {
                                    label: {
                                        show: true,
                                        position: 'top'
                                    }
                                }
                            }
                        }
                    ]
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }


    depositProductTrendQuery(ind, index) {//存款产品类别
        this.returnParams(ind, index);
        let params = {
            tellerId: this.commonHeader.userId,
            dmFlag: this.dmFlag,
            dwFlag: this.moneyGears,
            startDate: this.startDate,
            endDate: this.endDate,
        }
        this.customerPerformanceHttpService.depositProductTrendQuery(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let xData1 = [];
                let xData2 = [];
                let yData1 = [];
                let yData2 = [];
                let series1 = [];
                let series2 = [];
                let data1 = [];
                let data2 = [];
                if (data.myAmtList && data.myAmtList.length != 0) {
                    data.myAmtList.forEach(el => {
                        let arr = el.dt.split('-');
                        if (this.dmFlag == 'M') {
                            xData1.push(`${arr[0]}年${arr[1]}月`);
                        } else if (this.dmFlag == 'D') {
                            xData1.push(`${arr[0]}年${arr[1]}月${arr[2]}日`);
                        }
                    });
                    this.bigProductList.forEach(element => {
                        yData1 = [];
                        data.myAmtList.forEach(element2 => {
                            element2.myProductAmt.forEach(element3 => {
                                if (element3.productId == element.value) {
                                    data1.push({ name: element.label });
                                    yData1.push(element3.myAmt);
                                }
                            });
                        });
                        if (yData1 && yData1.length != 0) {
                            let objTemp = {};
                            objTemp['name'] = element.label;
                            objTemp['type'] = 'line';
                            objTemp['data'] = yData1;
                            objTemp['itemStyle'] = {
                                normal: {
                                    lineStyle: {
                                        type: 'dashed'
                                    },
                                    label: {
                                        show: true,
                                        formatter: (params) => {
                                            if (params.dataIndex == objTemp['data'].length - 1 || params.dataIndex == 0) {
                                                return params.value
                                            } else {
                                                return ''
                                            }
                                        }
                                    }
                                }
                            }
                            series1.push(objTemp);
                        }
                    });
                }

                if (data.myCustList && data.myCustList.length != 0) {
                    data.myCustList.forEach(el => {
                        let arr = el.dt.split('-');
                        if (this.dmFlag == 'M') {
                            xData2.push(`${arr[0]}年${arr[1]}月`);
                        } else if (this.dmFlag == 'D') {
                            xData2.push(`${arr[0]}年${arr[1]}月${arr[2]}日`);
                        }
                    });
                    this.bigProductList.forEach(element => {
                        yData2 = [];
                        data.myCustList.forEach(element2 => {
                            element2.myProductCust.forEach(element3 => {
                                if (element3.productId == element.value) {
                                    data2.push({ name: element.label });
                                    yData2.push(element3.myCust);
                                }
                            });
                        });
                        if (yData2 && yData2.length != 0) {
                            let objTemp = {};
                            objTemp['name'] = element.label;
                            objTemp['type'] = 'line';
                            objTemp['data'] = yData2;
                            objTemp['itemStyle'] = {
                                normal: {
                                    lineStyle: {
                                        type: 'dashed'
                                    },
                                    label: {
                                        show: true,
                                        formatter: (params) => {
                                            if (params.dataIndex == objTemp['data'].length - 1 || params.dataIndex == 0) {
                                                return params.value
                                            } else {
                                                return ''
                                            }
                                        }
                                    }
                                }
                            }
                            series2.push(objTemp);
                        }
                    });
                }
                if (ind == 0 || ind == 1) {
                    this.echoParams(1, data.dmFlag);
                    this.option3 = {
                        title: {
                            text: '各产品变化趋势',
                            left: 'center',
                            y: '10px',
                            textStyle: {
                                fontWeight: '500',
                                fontSize: '16'
                            }
                        },
                        legend: {
                            data: data1,
                            top: 'bottom',
                            itemGap: 20,
                            icon: 'line',
                            textStyle: {
                                color: '#34CFE6'
                            }
                        },
                        tooltip: {
                            trigger: 'axis',
                            axisPointer: {
                                type: 'cross'
                            }
                        },
                        grid: {
                            width: '80%',
                            height: '75%',
                            left: '10%',
                            bottom: '10%',
                            containLabel: true
                        },
                        xAxis: {
                            type: 'category',
                            data: xData2,
                            axisLabel: {
                                rotate: 30
                            },
                            axisTick: {
                                show: false
                            },
                            axisLine: {
                                show: false
                            }
                        },
                        yAxis: {
                            type: 'value',
                            name: '金额(万元)',
                            nameGap: 30,
                            scale: true,
                            splitArea: {
                                show: true
                            },
                            axisTick: {
                                show: false
                            },
                            axisLine: {
                                show: false
                            }
                        },
                        series: series1
                    }
                }

                if (ind == 0 || ind == 2) {
                    this.echoParams(2, data.dmFlag);
                    this.option6 = {
                        title: {
                            text: '各产品变化趋势',
                            left: 'center',
                            y: '10px',
                            textStyle: {
                                fontWeight: '500',
                                fontSize: '16'
                            }
                        },
                        legend: {
                            data: data2,
                            top: 'bottom',
                            itemGap: 20,
                            icon: 'line',
                            textStyle: {
                                color: '#34CFE6'
                            }
                        },
                        tooltip: {
                            trigger: 'axis',
                            axisPointer: {
                                type: 'cross'
                            }
                        },
                        grid: {
                            width: '80%',
                            height: '75%',
                            left: '10%',
                            bottom: '10%',
                            containLabel: true
                        },
                        xAxis: {
                            type: 'category',
                            data: xData2,
                            axisLabel: {
                                rotate: 30
                            },
                            axisTick: {
                                show: false
                            },
                            axisLine: {
                                show: false
                            }
                        },
                        yAxis: {
                            type: 'value',
                            name: '客户数',
                            nameGap: 30,
                            scale: true,
                            splitArea: {
                                show: true
                            },
                            axisTick: {
                                show: false
                            },
                            axisLine: {
                                show: false
                            }
                        },
                        series: series2
                    }
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }


}