    pTooltip3: string = '按日展示我的各产品每日贷款余额的变化趋势，按月展示自然月内各产品贷款日均变化趋势';
    pTooltip4: string = '截止到所选时间的最后一日，我的在贷贷款客户数在各产品的客户数与占比\n注：允许单个客户可同时持有多个产品，故合计占比有可能超过100%';
