import { Component, OnInit } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CustomerPerformanceHttpService } from 'app/pages/tzb/custom/http/customer-performance/customer-performance.http.service';
import { Message } from 'primeng/primeng';
import { CustomerPerformanceService } from '../../service/customer-performance.service';
import { ActivatedRoute } from '@angular/router';

@Component({
    selector: 'deposit-tendcyChange',
    templateUrl: './deposit-tendcyChange.component.html',
    styleUrls: ['./deposit-tendcyChange.component.scss'],
    providers: [Commfunc]
})

export class DepositTendcyChangeComponent implements OnInit {

    commonHeader: any;
    msgs: Message[] = [];
    dmFlag: string;
    dwFlag: string = '1';  // 档位
    gearList: any[] = [
        { label: '0以上', value: '1' },
        { label: '大于等于1000', value: '2' },
        { label: '大于等于5000', value: '3' },
        { label: '大于等于10000', value: '4' },
        { label: '大于等于20000', value: '5' },
        { label: '大于等于50000', value: '6' },
    ];
    showIndex1: number = 1;
    showIndex2: number = 1;
    showIndex3: number = 1;
    showIndex4: number = 1;
    startDate: any = this.commfunc.transDateN(new Date().setTime(new Date().getTime() - 1000 * 60 * 60 * 24 * 30));
    endDate: any = this.commfunc.transDateN(new Date());
    option1: any;
    option2: any;
    option3: any;
    pTooltip1: string = `我：按日展示自然月截止到当日的存款余额，按月展示自然月存款日均 \n 部门平均：按日展示每日部门合计存款余额/部门总营销人数，按月则日均同理`;
    pTooltip2: string = `我：按日展示每日存款余额在部门合计存款余额占比，按月展示自然月存款日均在部门合计存款日均占比 \n 部门平均：1/部门总营销人数，计算部门内人均占比 \n 部门第一名：按日展示每日部门中存款余额最高的客户经理对应的存款余额在部门合计存款余额中占比，按月则日均同理`;
    pTooltip3: string = `我：按日展示截止到每日持有存款客户数，按月展示自然月持有存款客户数 \n 部门平均：按日展示部门内合计截止到每日持有存款客户数/部门总营销人数，按月同理`;

    constructor(
        public commfunc: Commfunc,
        public customerPerformanceHttpService: CustomerPerformanceHttpService,
        public customerPerformanceService: CustomerPerformanceService,
        public activatedRoute: ActivatedRoute
    ) {
        this.commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        //初值
        this.option1 = this.customerPerformanceService.returnOption({ title: '存款金额', legend: [{ name: '我' }, { name: '部门平均' }], xAxisData: [], yAxisName: '金额(万元)', series: [] });
        this.option2 = this.customerPerformanceService.returnOption({ title: '存款金额占比', legend: [{ name: '我' }, { name: '部门平均' }], xAxisData: [], yAxisName: '占比', series: [] });
        this.option3 = this.customerPerformanceService.returnOption({ title: '存款客户数', legend: [{ name: '我' }, { name: '部门平均' }], xAxisData: [], yAxisName: '客户数', series: [] });
    }

    ngOnInit() {
        let urlParams;
        this.activatedRoute.params.subscribe(params => {
            urlParams = params;
        });
        // console.log(urlParams);
        if (urlParams && JSON.stringify(urlParams) != '{}') {
            if (urlParams.flag != '1') {
                this.depositAllAmtQuery(0, 1);
            }
        }
        // this.loansProportionQuery(2, 1);
        // this.loansCustNumQuery(3, 1);
    }

    query() {
        this.depositAllAmtQuery(0, 1);
    }

    outValue(event) {
        let objTemp;
        if (event.index == 1) {
            objTemp = this.customerPerformanceService.returnTime(event.queryCondition, event.index);
        } else if (event.index == 2) {
            objTemp = event.queryCondition;
        }
        this.startDate = objTemp.startDate;
        this.endDate = objTemp.endDate;
        console.log('startDate:', this.startDate);
        console.log('endDate:', this.endDate);
        this.depositAllAmtQuery(0, 1);
    }

    returnParams(ind, index) {//修改日/月
        this[`showIndex${ind}`] = index;
        if (index == 1) {
            this.dmFlag = 'D';
        } else {
            this.dmFlag = 'M';
        }
    }

    echoParams(ind, flag) {
        let index;
        if (flag == 'D') {
            index = 1;
        } else {
            index = 2;
        }
        this[`showIndex${ind}`] = index;
    }

    depositAllAmtQuery(ind, index) {//存款金额
        this.returnParams(ind, index);
        let params = {
            tellerId: this.commonHeader.userId,
            dmFlag: this.dmFlag,
            dwFlag: this.dwFlag,
            startDate: this.startDate,
            endDate: this.endDate
        }
        this.customerPerformanceHttpService.depositTrendQuery(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let xData1 = [];
                let xData2 = [];
                let xData3 = [];
                let yData1 = [];
                let yData2 = [];
                let yData3 = [];
                let yData4 = [];
                let yData5 = [];
                let yData6 = [];
                let yData7 = [];
                let yData8 = [];
                let yData9 = [];
                if (data.myDepositList && data.myDepositList.length != 0 && data.departmentDepositList && data.departmentDepositList.length != 0 && data.brachDepositList && data.brachDepositList.length != 0) {
                    data.myDepositList.forEach(element => {
                        if (this.dmFlag == 'M') {
                            let arr = element.dt.split('-');
                            xData1.push(`${arr[0]}年${arr[1]}月`);
                            yData1.push(element.depositAmt);
                        } else if (this.dmFlag == 'D') {
                            let arr = element.dt.split('-');
                            xData1.push(`${arr[0]}年${arr[1]}月${arr[2]}日`);
                            yData1.push(element.depositAmt);
                        }

                    });
                    data.departmentDepositList.forEach(element => {
                        yData2.push(element.depDepositAmt);
                    });
                    data.brachDepositList.forEach(element => {
                        yData3.push(element.brachDepositAmt);
                    });
                }

                if (data.myDepositProList && data.myDepositProList.length != 0 && data.departmentDepositProList && data.departmentDepositProList.length != 0 && data.departmentDepositProList && data.departmentDepositProList.length != 0) {
                    data.myDepositProList.forEach(element => {
                        if (this.dmFlag == 'M') {
                            let arr = element.dt.split('-');
                            xData2.push(`${arr[0]}年${arr[1]}月`);
                            yData4.push(Number(element.depositAmtPt * 100).toFixed(2));
                        } else if (this.dmFlag == 'D') {
                            let arr = element.dt.split('-');
                            xData2.push(`${arr[0]}年${arr[1]}月${arr[2]}日`);
                            yData4.push(Number(element.depositAmtPt * 100).toFixed(2));
                        }

                    });
                    data.departmentDepositProList.forEach(element => {
                        yData5.push(Number(element.depDepositAmtPt * 100).toFixed(2));
                    });
                    data.departmentOneDepositProList.forEach(element => {
                        yData6.push(Number(element.depOneDepositAmtPt * 100).toFixed(2));
                    });
                }

                if (data.myCustList && data.myCustList.length != 0 && data.departmentCustList && data.departmentCustList.length != 0 && data.brachCustList && data.brachCustList.length != 0) {
                    data.myCustList.forEach(element => {
                        if (this.dmFlag == 'M') {
                            let arr = element.dt.split('-');
                            xData3.push(`${arr[0]}年${arr[1]}月`);
                            yData7.push(Number(element.depositCust).toFixed(2));
                        } else if (this.dmFlag == 'D') {
                            let arr = element.dt.split('-');
                            xData3.push(`${arr[0]}年${arr[1]}月${arr[2]}日`);
                            yData7.push(Number(element.depositCust).toFixed(2));
                        }
                    });
                    data.departmentCustList.forEach(element => {
                        yData8.push(element.depDdepositCust);
                    });
                    data.brachCustList.forEach(element => {
                        yData9.push(element.brachDepositCust);
                    });
                }
                if (ind == '0' || ind == '1') {
                    this.echoParams(1, data.dmFlag);
                    this.option1 = {
                        title: {
                            text: '存款金额',
                            left: 'center',
                            y: '10px',
                            textStyle: {
                                fontWeight: '500',
                                fontSize: '16'
                            }
                        },
                        legend: {
                            data: [{ name: '我' }, { name: '部门平均' }],
                            top: 'bottom',
                            itemGap: 20,
                            icon: 'line',
                            textStyle: {
                                color: '#34CFE6'
                            }
                        },
                        tooltip: {
                            trigger: 'axis',
                            axisPointer: {
                                type: 'cross'
                            }
                        },
                        grid: {
                            width: '80%',
                            height: '75%',
                            left: '10%',
                            bottom: '10%',
                            containLabel: true
                        },
                        xAxis: {
                            type: 'category',
                            data: xData1,
                            axisLabel: {
                                rotate: 30
                            },
                            axisTick: {
                                show: false
                            },
                            axisLine: {
                                show: false
                            }
                        },
                        yAxis: {
                            type: 'value',
                            name: '金额(万元)',
                            nameGap: 30,
                            scale: true,
                            splitArea: {
                                show: true
                            },
                            axisTick: {
                                show: false
                            },
                            axisLine: {
                                show: false
                            }
                        },
                        series: [
                            {
                                name: '我',
                                data: yData1,
                                type: 'line',
                                itemStyle: {
                                    normal: {
                                        label: {
                                            show: true,
                                            formatter: (params) => {
                                                if (params.dataIndex == yData1.length - 1 || params.dataIndex == 0) {
                                                    return params.value
                                                } else {
                                                    return ''
                                                }
                                            }
                                        }
                                    }
                                }
                            },
                            {
                                name: '部门平均',
                                data: yData2,
                                type: 'line',
                                itemStyle: {
                                    normal: {
                                        lineStyle: {
                                            type: 'dashed'
                                        },
                                        label: {
                                            show: true,
                                            formatter: (params) => {
                                                if (params.dataIndex == yData2.length - 1 || params.dataIndex == 0) {
                                                    return params.value
                                                } else {
                                                    return ''
                                                }
                                            }
                                        }
                                    }
                                }
                            },
                            // {
                            //     name: '分行优秀线',
                            //     data: yData3,
                            //     type: 'line',
                            //     itemStyle: {
                            //         normal: {
                            //             lineStyle: {
                            //                 type: 'dashed'
                            //             },
                            //             label: {
                            //                 show: true,
                            //                 formatter: (params) => {
                            //                     if (params.dataIndex == yData3.length - 1 || params.dataIndex == 0) {
                            //                         return params.value
                            //                     } else {
                            //                         return ''
                            //                     }
                            //                 }
                            //             }
                            //         }
                            //     }
                            // },
                        ]
                    };
                }

                if (ind == '0' || ind == '2') {
                    this.echoParams(2, data.dmFlag);
                    this.option2 = {
                        title: {
                            text: '存款金额占比',
                            left: 'center',
                            y: '10px',
                            textStyle: {
                                fontWeight: '500',
                                fontSize: '16'
                            }
                        },
                        legend: {
                            data: [{ name: '我' }, { name: '部门平均' }, { name: '同部门第一名' }],
                            top: 'bottom',
                            itemGap: 20,
                            icon: 'line',
                            textStyle: {
                                color: '#34CFE6'
                            }
                        },
                        tooltip: {
                            trigger: 'axis',
                            axisPointer: {
                                type: 'cross'
                            }
                        },
                        grid: {
                            width: '80%',
                            height: '75%',
                            left: '10%',
                            bottom: '10%',
                            containLabel: true
                        },
                        xAxis: {
                            type: 'category',
                            data: xData2,
                            axisLabel: {
                                rotate: 30
                            },
                            axisTick: {
                                show: false
                            },
                            axisLine: {
                                show: false
                            }
                        },
                        yAxis: {
                            type: 'value',
                            name: '占比',
                            nameGap: 30,
                            scale: true,
                            axisLabel: {
                                formatter: '{value}%'
                            },
                            splitArea: {
                                show: true
                            },
                            axisTick: {
                                show: false
                            },
                            axisLine: {
                                show: false
                            }
                        },
                        series: [
                            {
                                name: '我',
                                data: yData4,
                                type: 'line',
                                itemStyle: {
                                    normal: {
                                        label: {
                                            show: true,
                                            formatter: (params) => {
                                                if (params.dataIndex == yData4.length - 1) {
                                                    return params.value
                                                } else {
                                                    return ''
                                                }
                                            }
                                        }
                                    }
                                }
                            },
                            {
                                name: '部门平均',
                                data: yData5,
                                type: 'line',
                                itemStyle: {
                                    normal: {
                                        lineStyle: {
                                            type: 'dashed'
                                        },
                                        label: {
                                            show: true,
                                            formatter: (params) => {
                                                if (params.dataIndex == yData5.length - 1) {
                                                    return params.value
                                                } else {
                                                    return ''
                                                }
                                            }
                                        }
                                    }
                                }
                            },
                            {
                                name: '同部门第一名',
                                data: yData6,
                                type: 'line',
                                itemStyle: {
                                    normal: {
                                        lineStyle: {
                                            type: 'dashed'
                                        },
                                        label: {
                                            show: true,
                                            formatter: (params) => {
                                                if (params.dataIndex == yData6.length - 1) {
                                                    return params.value
                                                } else {
                                                    return ''
                                                }
                                            }
                                        }
                                    }
                                }
                            },
                        ]
                    }
                }

                if (ind == '0' || ind == '3') {
                    this.echoParams(3, data.dmFlag);
                    this.option3 = {
                        title: {
                            text: '存款客户数',
                            left: 'center',
                            y: '10px',
                            textStyle: {
                                fontWeight: '500',
                                fontSize: '16'
                            }
                        },
                        legend: {
                            data: [{ name: '我' }, { name: '部门平均' }],
                            top: 'bottom',
                            itemGap: 20,
                            icon: 'line',
                            textStyle: {
                                color: '#34CFE6'
                            }
                        },
                        tooltip: {
                            trigger: 'axis',
                            axisPointer: {
                                type: 'cross'
                            }
                        },
                        grid: {
                            width: '80%',
                            height: '75%',
                            left: '10%',
                            bottom: '10%',
                            containLabel: true
                        },
                        xAxis: {
                            type: 'category',
                            data: xData3,
                            axisLabel: {
                                rotate: 30
                            },
                            axisTick: {
                                show: false
                            },
                            axisLine: {
                                show: false
                            }
                        },
                        yAxis: {
                            type: 'value',
                            name: '客户数',
                            nameGap: 30,
                            scale: true,
                            splitArea: {
                                show: true
                            },
                            axisTick: {
                                show: false
                            },
                            axisLine: {
                                show: false
                            }
                        },
                        series: [
                            {
                                name: '我',
                                data: yData7,
                                type: 'line',
                                itemStyle: {
                                    normal: {
                                        label: {
                                            show: true,
                                            formatter: (params) => {
                                                if (params.dataIndex == yData7.length - 1 || params.dataIndex == 0) {
                                                    return params.value
                                                } else {
                                                    return ''
                                                }
                                            }
                                        }
                                    }
                                }
                            },
                            {
                                name: '部门平均',
                                data: yData8,
                                type: 'line',
                                itemStyle: {
                                    normal: {
                                        lineStyle: {
                                            type: 'dashed'
                                        },
                                        label: {
                                            show: true,
                                            formatter: (params) => {
                                                if (params.dataIndex == yData8.length - 1 || params.dataIndex == 0) {
                                                    return params.value
                                                } else {
                                                    return ''
                                                }
                                            }
                                        }
                                    }
                                }
                            },
                            // {
                            //     name: '分行优秀线',
                            //     data: yData9,
                            //     type: 'line',
                            //     itemStyle: {
                            //         normal: {
                            //             lineStyle: {
                            //                 type: 'dashed'
                            //             },
                            //             label: {
                            //                 show: true,
                            //                 formatter: (params) => {
                            //                     if (params.dataIndex == yData9.length - 1 || params.dataIndex == 0) {
                            //                         return params.value
                            //                     } else {
                            //                         return ''
                            //                     }
                            //                 }
                            //             }
                            //         }
                            //     }
                            // },
                        ]
                    }
                }

            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
}