import { Component, OnInit } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CustomerPerformanceHttpService } from 'app/pages/tzb/custom/http/customer-performance/customer-performance.http.service';
import { Message } from 'primeng/primeng';
import { CustomerPerformanceService } from '../../service/customer-performance.service';
import { ActivatedRoute } from '@angular/router';

@Component({
    selector: 'deposit-smallClassProduct',
    templateUrl: './deposit-smallClassProduct.component.html',
    styleUrls: ['./deposit-smallClassProduct.component.scss'],
    providers: [Commfunc]
})

export class DepositSmallClassProductComponent implements OnInit {

    commonHeader: any;
    msgs: Message[] = [];
    option1: any;
    option2: any;
    showIndex1: number = 1;
    showIndex2: number = 1;
    dmFlag: string;
    dwFlag: string = '1';
    gearList: any[] = [
        { label: '0以上', value: '1' },
        { label: '大于等于1000', value: '2' },
        { label: '大于等于5000', value: '3' },
        { label: '大于等于10000', value: '4' },
        { label: '大于等于20000', value: '5' },
        { label: '大于等于50000', value: '6' },
    ];
    startDate: any = this.commfunc.transDateN(new Date().setTime(new Date().getTime() - 1000 * 60 * 60 * 24 * 30));
    endDate: any = this.commfunc.transDateN(new Date());
    pTooltip1: string = '金额变化：按日展示所选产品每日余额变化，按月展示所选产品自然月日均变化\n占比变化：按日展示所选产品每日余额在所有存款产品余额占比，按月展示所选产品存款日均在所有存款日均占比';
    pTooltip2: string = '人数变化：按日展示所选产品每日持有客户数变化，按月展示所选产品自然月持有客户数变化\n占比变化：按日展示所选产品每日持有客户数在所有存款产品客户数占比，按月展示所选产品自然月持有客户在所有存款持有客户占比';
    bigProductList: any = [
        { label: '请选择', value: '' },
        { label: '金添储蓄', value: 'L2011' },
        { label: '个人活期', value: 'L2012' },
        { label: '单位活期存款', value: 'L2013' },
        { label: '单位定期存款', value: 'L2014' },
        { label: '保证金存款', value: 'L2015' },
        { label: '其他存款', value: 'L2016' }
    ]
    code: any;
    productBigId: string = this.bigProductList[0].value;
    productBigName: string = this.bigProductList[0].label;
    productSmallId: string;
    urlParams: any;
    moneyGears: string = '';
    smallProductList: any;
    smallProductList1: any;
    smallProductList2: any;
    smallProductList3: any;
    smallProductList4: any;
    smallProductList5: any;
    smallProductList6: any;
    constructor(
        public commfunc: Commfunc,
        public customerPerformanceHttpService: CustomerPerformanceHttpService,
        public customerPerformanceService: CustomerPerformanceService,
        public activatedRoute: ActivatedRoute,
    ) {
        this.commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        //初值
        this.option1 = this.customerPerformanceService.returnOption({ title: '金额变化趋势', legend: [], xAxisData: [], yAxisName: '金额(万元)', series: [] });
        this.option2 = this.customerPerformanceService.returnOption({ title: '客户数变化趋势', legend: [], xAxisData: [], yAxisName: '客户数', series: [] });
        this.codeVal();
        this.bigProductList = this.code['depositProdBigType'];
        this.bigProductList.unshift({ label: '请选择', value: '' });
        this.smallProductList1 = this.code['depositProdSmallType1'];
        this.smallProductList1.unshift({ label: '总计', value: 'total' });
        this.smallProductList1.unshift({ label: '请选择', value: '' });
        this.smallProductList2 = this.code['depositProdSmallType2'];
        this.smallProductList3 = this.code['depositProdSmallType3'];
        this.smallProductList4 = this.code['depositProdSmallType4'];
        this.smallProductList5 = this.code['depositProdSmallType5'];
        this.smallProductList6 = this.code['depositProdSmallType6'];
    }

    ngOnInit() {
        let urlParams;
        this.activatedRoute.params.subscribe(params => {
            urlParams = params;
        });
        if (urlParams && JSON.stringify(urlParams) != '{}') {
            if (urlParams.flag != '1') {
                this.depositSmallProductQuery(0, 1);
            }
            this.productBigId = urlParams.productId;
            this.productSmallId = urlParams.smallClassProductId;
            this.changeBigType('init');
        }
    }

    outValue(event) {
        let objTemp;
        if (event.index == 1) {
            objTemp = this.customerPerformanceService.returnTime(event.queryCondition, event.index);
        } else if (event.index == 2) {
            objTemp = event.queryCondition;
        }
        this.startDate = objTemp.startDate;
        this.endDate = objTemp.endDate;
        console.log('startDate:', this.startDate);
        console.log('endDate:', this.endDate);
        this.depositSmallProductQuery(0, 1);
    }

    returnParams(ind, index) {
        this[`showIndex${ind}`] = index;
        if (index == 1) {
            this.dmFlag = 'D';
        } else {
            this.dmFlag = 'M';
        }
    }

    echoParams(ind, flag) {
        let index;
        if (flag == 'D') {
            index = 1;
        } else {
            index = 2;
        }
        this[`showIndex${ind}`] = index;
    }

    // 点击查询
    query() {
        this.depositSmallProductQuery(0, 1);
    }

    depositSmallProductQuery(ind, index) {//存款产品小类
        this.returnParams(ind, index);
        if (this.productSmallId && this.productSmallId == 'total') {//合计
            let params = {
                tellerId: this.commonHeader.userId,
                dmFlag: this.dmFlag,
                dwFlag: this.dwFlag,
                startDate: this.startDate,
                endDate: this.endDate,
                productId: this.productBigId
            }
            this.customerPerformanceHttpService.depositProductTrendQuery(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    let xData1 = [];
                    let xData2 = [];
                    let yData1 = [];
                    let yData1_1 = [];
                    let yData2 = [];
                    let yData2_2 = [];
                    let yData3 = [];
                    let yData4 = [];
                    let series1 = [];
                    let series2 = [];
                    let data1 = [];
                    let data2 = [];
                    if (data.myAmtList && data.myAmtList.length != 0) {
                        data.myAmtList.forEach(element => {
                            let arr = element.dt.split('-');
                            if (data.dmFlag && data.dmFlag == 'D') {
                                xData1.push(`${arr[0]}年${arr[1]}月${arr[2]}日`);
                            } else if (data.dmFlag && data.dmFlag == 'M') {
                                xData1.push(`${arr[0]}年${arr[1]}月`);
                            }
                        });
                        this.bigProductList.forEach(element => {
                            yData1 = [];
                            yData1_1 = [];
                            data.myAmtList.forEach(element2 => {
                                element2.myProductAmt.forEach(element3 => {
                                    if (element3.productId == element.value) {
                                        data1.push({ name: element.label });
                                        yData1.push(element3.myAmt);
                                        yData1_1.push(Number(element3.jezb * 100).toFixed(2));
                                    }
                                });
                            });
                            if (yData1 && yData1.length != 0) {
                                let length = yData1.length;
                                let objTemp = {};
                                objTemp['name'] = '金额变化';
                                objTemp['type'] = 'line';
                                objTemp['yAxisIndex'] = 0;
                                objTemp['itemStyle'] = {
                                    normal: {
                                        label: {
                                            show: true,
                                            formatter: (params) => {
                                                if (params.dataIndex == 0 || params.dataIndex == length - 1) {
                                                    return params.value
                                                } else {
                                                    return ''
                                                }
                                            }
                                        }
                                    }
                                }
                                objTemp['data'] = yData1;
                                series1.push(objTemp);
                            }
                            if (yData1_1 && yData1_1.length != 0) {
                                let length = yData1_1.length;
                                let objTemp = {};
                                objTemp['name'] = '金额占比';
                                objTemp['type'] = 'line';
                                objTemp['yAxisIndex'] = 1;
                                objTemp['itemStyle'] = {
                                    normal: {
                                        label: {
                                            show: true,
                                            formatter: (params) => {
                                                if (params.dataIndex == 0 || params.dataIndex == length - 1) {
                                                    return params.value
                                                } else {
                                                    return ''
                                                }
                                            }
                                        }
                                    }
                                }
                                objTemp['data'] = yData1_1;
                                series1.push(objTemp);
                            }
                        });
                        console.log(series1);
                    }
                    if (data.myCustList && data.myCustList.length != 0) {
                        data.myCustList.forEach(element => {
                            let arr = element.dt.split('-');
                            if (data.dmFlag && data.dmFlag == 'D') {
                                xData2.push(`${arr[0]}年${arr[1]}月${arr[2]}日`);
                            } else if (data.dmFlag && data.dmFlag == 'M') {
                                xData2.push(`${arr[0]}年${arr[1]}月`);
                            }
                        });
                        this.bigProductList.forEach(element => {
                            yData2 = [];
                            yData2_2 = [];
                            data.myCustList.forEach(element2 => {
                                element2.myProductCust.forEach(element3 => {
                                    if (element3.productId == element.value) {
                                        data2.push({ name: element.label });
                                        yData2.push(element3.myCust);
                                        yData2_2.push(Number(element3.khszb * 100).toFixed(2));
                                    }
                                });
                            });
                            if (yData2 && yData2.length != 0) {
                                let length = yData2.length;
                                let objTemp = {};
                                objTemp['name'] = '客户数';
                                objTemp['type'] = 'line';
                                objTemp['yAxisIndex'] = 0;
                                objTemp['itemStyle'] = {
                                    normal: {
                                        label: {
                                            show: true,
                                            formatter: (params) => {
                                                if (params.dataIndex == 0 || params.dataIndex == length - 1) {
                                                    return params.value
                                                } else {
                                                    return ''
                                                }
                                            }
                                        }
                                    }
                                }
                                objTemp['data'] = yData2;
                                series2.push(objTemp);
                            }
                            if (yData2_2 && yData2_2.length != 0) {
                                let length = yData2_2.length;
                                let objTemp = {};
                                objTemp['name'] = '占比';
                                objTemp['type'] = 'line';
                                objTemp['yAxisIndex'] = 1;
                                objTemp['itemStyle'] = {
                                    normal: {
                                        label: {
                                            show: true,
                                            formatter: (params) => {
                                                if (params.dataIndex == 0 || params.dataIndex == length - 1) {
                                                    return params.value
                                                } else {
                                                    return ''
                                                }
                                            }
                                        }
                                    }
                                }
                                objTemp['data'] = yData2_2;
                                series2.push(objTemp);
                            }
                        });
                        console.log(series2);
                    }
                    let productList = this.bigProductList.filter((item) => {
                        return item.value == this.productBigId
                    });
                    if (ind == 0 || ind == 1) {
                        this.echoParams(1, data.dmFlag);
                        let arrTemp = this.calMax([series1[0].data, series1[1].data]);//解决echarts的双y轴重叠问题
                        this.option1 = {
                            title: {
                                text: `${productList[0].label}金额变化趋势`,
                                left: 'center',
                                y: '10px',
                                textStyle: {
                                    fontWeight: '500',
                                    fontSize: '16'
                                }
                            },
                            legend: {
                                data: [{ name: '金额变化' }, { name: '金额占比' }],
                                top: 'bottom',
                                itemGap: 20,
                                icon: 'line',
                                textStyle: {
                                    color: '#34CFE6'
                                }
                            },
                            tooltip: {
                                trigger: 'axis',
                                axisPointer: {
                                    type: 'cross'
                                }
                            },
                            grid: {
                                width: '80%',
                                height: '75%',
                                left: '10%',
                                bottom: '10%',
                                containLabel: true
                            },
                            xAxis: {
                                type: 'category',
                                data: xData1,
                                axisLabel: {
                                    rotate: 30
                                },
                                axisTick: {
                                    show: false
                                },
                                axisLine: {
                                    show: false
                                }
                            },
                            yAxis: [
                                {
                                    name: '金额(万元)',
                                    type: 'value',
                                    nameGap: 30,
                                    scale: true,
                                    min: arrTemp[0],
                                    max: arrTemp[1],
                                    splitNumber: 5,
                                    interval: (arrTemp[1] - arrTemp[0]) / 5,
                                    splitArea: {
                                        show: true
                                    },
                                    axisTick: {
                                        show: false
                                    },
                                    axisLine: {
                                        show: false
                                    },
                                }, {
                                    name: '金额占比',
                                    type: 'value',
                                    nameGap: 30,
                                    scale: true,
                                    min: arrTemp[0],
                                    max: arrTemp[1],
                                    splitNumber: 5,
                                    interval: (arrTemp[1] - arrTemp[0]) / 5,
                                    splitArea: {
                                        show: true
                                    },
                                    axisTick: {
                                        show: false
                                    },
                                    axisLine: {
                                        show: false
                                    },
                                    axisLabel: {
                                        formatter: '{value}%'
                                    }
                                }
                            ],
                            series: series1
                        }
                    }

                    if (ind == 0 || ind == 2) {
                        this.echoParams(2, data.dmFlag);
                        let arrTemp = this.calMax([series2[0].data, series2[1].data]);//解决echarts的双y轴重叠问题
                        this.option2 = {
                            title: {
                                text: `${productList[0].label}客户数变化趋势`,
                                left: 'center',
                                y: '10px',
                                textStyle: {
                                    fontWeight: '500',
                                    fontSize: '16'
                                }
                            },
                            legend: {
                                data: [{ name: '客户数' }, { name: '占比' }],
                                top: 'bottom',
                                itemGap: 20,
                                icon: 'line',
                                textStyle: {
                                    color: '#34CFE6'
                                }
                            },
                            tooltip: {
                                trigger: 'axis',
                                axisPointer: {
                                    type: 'cross'
                                }
                            },
                            grid: {
                                width: '80%',
                                height: '75%',
                                left: '10%',
                                bottom: '10%',
                                containLabel: true
                            },
                            xAxis: {
                                type: 'category',
                                data: xData2,
                                axisLabel: {
                                    rotate: 30
                                },
                                axisTick: {
                                    show: false
                                },
                                axisLine: {
                                    show: false
                                }
                            },
                            yAxis: [
                                {
                                    name: '客户数',
                                    type: 'value',
                                    nameGap: 30,
                                    scale: true,
                                    min: arrTemp[0],
                                    max: arrTemp[1],
                                    splitNumber: 5,
                                    interval: (arrTemp[1] - arrTemp[0]) / 5,
                                    splitArea: {
                                        show: true
                                    },
                                    axisTick: {
                                        show: false
                                    },
                                    axisLine: {
                                        show: false
                                    },
                                }, {
                                    name: '占比',
                                    type: 'value',
                                    nameGap: 30,
                                    scale: true,
                                    min: arrTemp[0],
                                    max: arrTemp[1],
                                    splitNumber: 5,
                                    interval: (arrTemp[1] - arrTemp[0]) / 5,
                                    splitArea: {
                                        show: true
                                    },
                                    axisTick: {
                                        show: false
                                    },
                                    axisLine: {
                                        show: false
                                    },
                                    axisLabel: {
                                        formatter: '{value}%'
                                    }
                                }
                            ],
                            series: series2
                        }
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
            });
        } else {
            let params = {
                tellerId: this.commonHeader.userId,
                dmFlag: this.dmFlag,
                dwFlag: this.dwFlag,
                startDate: this.startDate,
                endDate: this.endDate,
                productId: this.productSmallId
            }
            this.customerPerformanceHttpService.depositSmallProductQuery(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    let xData1 = [];
                    let xData2 = [];
                    let yData1 = [];
                    let yData2 = [];
                    let yData3 = [];
                    let yData4 = [];
                    if (data.depositProductAmtList && data.depositProductAmtList.length != 0) {
                        data.depositProductAmtList.forEach(element => {
                            let arr = element.dt.split('-');
                            if (this.dmFlag == 'M') {
                                xData1.push(`${arr[0]}年${arr[1]}月`);
                            } else if (this.dmFlag == 'D') {
                                xData1.push(`${arr[0]}年${arr[1]}月${arr[2]}日`);
                            }
                            yData1.push(element.depositAmt);
                        });
                    }
                    if (data.depositProductAmtPtList && data.depositProductAmtPtList.length != 0) {
                        data.depositProductAmtPtList.forEach(element => {
                            yData2.push(Number(element.depositAmtPt * 100).toFixed(2));
                        });
                    }
                    if (data.depositProductCustList && data.depositProductCustList.length != 0) {
                        data.depositProductCustList.forEach(element => {
                            let arr = element.dt.split('-');
                            if (this.dmFlag == 'M') {
                                xData2.push(`${arr[0]}年${arr[1]}月`);
                            } else if (this.dmFlag == 'D') {
                                xData2.push(`${arr[0]}年${arr[1]}月${arr[2]}日`);
                            }
                            yData3.push(element.depositCust);
                        });
                    }
                    if (data.depositProductCustPtList && data.depositProductCustPtList.length != 0) {
                        data.depositProductCustPtList.forEach(element => {
                            yData4.push(Number(element.depositCustPt * 100).toFixed(2));
                        });
                    }
                    let productList = this.smallProductList.filter((item) => {
                        return item.value == this.productSmallId
                    });
                    if (ind == 0 || ind == 1) {
                        this.echoParams(1, data.dmFlag);
                        this.option1 = {
                            title: {
                                text: `${productList[0].label}变化趋势`,
                                left: 'center',
                                y: '10px',
                                textStyle: {
                                    fontWeight: '500',
                                    fontSize: '16'
                                }
                            },
                            legend: {
                                data: [{ name: '金额变化' }, { name: '金额占比' }],
                                top: 'bottom',
                                itemGap: 20,
                                icon: 'line',
                                textStyle: {
                                    color: '#34CFE6'
                                }
                            },
                            tooltip: {
                                trigger: 'axis',
                                axisPointer: {
                                    type: 'cross'
                                }
                            },
                            grid: {
                                width: '80%',
                                height: '75%',
                                left: '10%',
                                bottom: '10%',
                                containLabel: true
                            },
                            xAxis: {
                                type: 'category',
                                data: xData1,
                                axisLabel: {
                                    rotate: 30
                                },
                                axisTick: {
                                    show: false
                                },
                                axisLine: {
                                    show: false
                                }
                            },
                            yAxis: [
                                {
                                    name: '金额(万元)',
                                    type: 'value',
                                    nameGap: 30,
                                    scale: true,
                                    splitArea: {
                                        show: true
                                    },
                                    axisTick: {
                                        show: false
                                    },
                                    axisLine: {
                                        show: false
                                    },
                                }, {
                                    name: '金额占比',
                                    type: 'value',
                                    nameGap: 30,
                                    splitArea: {
                                        show: true
                                    },
                                    axisTick: {
                                        show: false
                                    },
                                    axisLine: {
                                        show: false
                                    },
                                    axisLabel: {
                                        formatter: '{value}%'
                                    }
                                }
                            ],
                            series: [
                                {
                                    name: '金额变化',
                                    data: yData1,
                                    type: 'line',
                                    yAxisIndex: 0,
                                    itemStyle: {
                                        normal: {
                                            label: {
                                                show: true,
                                                formatter: (params) => {
                                                    if (params.dataIndex == yData1.length - 1 || params.dataIndex == 0) {
                                                        return params.value
                                                    } else {
                                                        return ''
                                                    }
                                                }
                                            }
                                        }
                                    }
                                },
                                {
                                    name: '金额占比',
                                    data: yData2,
                                    type: 'line',
                                    yAxisIndex: 1,
                                    itemStyle: {
                                        normal: {
                                            lineStyle: {
                                                type: 'dashed'
                                            },
                                            label: {
                                                show: true,
                                                formatter: (params) => {
                                                    if (params.dataIndex == yData2.length - 1 || params.dataIndex == 0) {
                                                        return params.value
                                                    } else {
                                                        return ''
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                            ]
                        }
                    }

                    if (ind == 0 || ind == 2) {
                        this.echoParams(2, data.dmFlag);
                        this.option2 = {
                            title: {
                                text: `${productList[0].label}变化趋势`,
                                left: 'center',
                                y: '10px',
                                textStyle: {
                                    fontWeight: '500',
                                    fontSize: '16'
                                }
                            },
                            legend: {
                                data: [{ name: '客户数' }, { name: '占比' }],
                                top: 'bottom',
                                itemGap: 20,
                                icon: 'line',
                                textStyle: {
                                    color: '#34CFE6'
                                }
                            },
                            tooltip: {
                                trigger: 'axis',
                                axisPointer: {
                                    type: 'cross'
                                }
                            },
                            grid: {
                                width: '80%',
                                height: '75%',
                                left: '10%',
                                bottom: '10%',
                                containLabel: true
                            },
                            xAxis: {
                                type: 'category',
                                data: xData2,
                                axisLabel: {
                                    rotate: 30
                                },
                                axisTick: {
                                    show: false
                                },
                                axisLine: {
                                    show: false
                                }
                            },
                            yAxis: [
                                {
                                    name: '客户数',
                                    type: 'value',
                                    scale: true,
                                    nameGap: 30,
                                    splitArea: {
                                        show: true
                                    },
                                    axisTick: {
                                        show: false
                                    },
                                    axisLine: {
                                        show: false
                                    }
                                }, {
                                    name: '占比',
                                    type: 'value',
                                    nameGap: 30,
                                    splitArea: {
                                        show: true
                                    },
                                    axisTick: {
                                        show: false
                                    },
                                    axisLine: {
                                        show: false
                                    },
                                    axisLabel: {
                                        formatter: '{value}%'
                                    }
                                }
                            ],
                            series: [
                                {
                                    name: '客户数',
                                    data: yData3,
                                    type: 'line',
                                    yAxisIndex: 0,
                                    itemStyle: {
                                        normal: {
                                            label: {
                                                show: true,
                                                formatter: (params) => {
                                                    if (params.dataIndex == yData3.length - 1 || params.dataIndex == 0) {
                                                        return params.value
                                                    } else {
                                                        return ''
                                                    }
                                                }
                                            }
                                        }
                                    }
                                },
                                {
                                    name: '占比',
                                    data: yData4,
                                    type: 'line',
                                    yAxisIndex: 1,
                                    itemStyle: {
                                        normal: {
                                            lineStyle: {
                                                type: 'dashed'
                                            },
                                            label: {
                                                show: true,
                                                formatter: (params) => {
                                                    if (params.dataIndex == yData4.length - 1 || params.dataIndex == 0) {
                                                        return params.value
                                                    } else {
                                                        return ''
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                            ]
                        }
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
            });
        }
    }

    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    changeBigType(flag?) {
        if (!flag) {
            this.productSmallId = '';
        }
        switch (this.productBigId) {
            case 'L2011':
                this.smallProductList = this.smallProductList1;
                break;
            case 'L2012':
                this.smallProductList2.unshift({ label: '总计', value: 'total' });
                this.smallProductList2.unshift({ label: '请选择', value: '' });
                this.smallProductList = this.smallProductList2;
                break;
            case 'L2013':
                this.smallProductList3.unshift({ label: '总计', value: 'total' });
                this.smallProductList3.unshift({ label: '请选择', value: '' });
                this.smallProductList = this.smallProductList3;
                break;
            case 'L2014':
                this.smallProductList4.unshift({ label: '总计', value: 'total' });
                this.smallProductList4.unshift({ label: '请选择', value: '' });
                this.smallProductList = this.smallProductList4;
                break;
            case 'L2015':
                this.smallProductList5.unshift({ label: '总计', value: 'total' });
                this.smallProductList5.unshift({ label: '请选择', value: '' });
                this.smallProductList = this.smallProductList5;
                break;
            case 'L2016':
                this.smallProductList6.unshift({ label: '总计', value: 'total' });
                this.smallProductList6.unshift({ label: '请选择', value: '' });
                this.smallProductList = this.smallProductList6;
                break;
        }
    }

    //计算最大值、最小值
    calMax(arr) {
        let max = 0;
        let min = 0;
        if (arr && arr.length != 0) {
            arr.forEach(element => {
                element.forEach(element2 => {
                    if (parseFloat(element2) < min) {
                        min = parseFloat(element2);
                    }
                    if (parseFloat(element2) > max) {
                        max = parseFloat(element2);
                    }
                });
            });
        }
        return [Math.floor(min / 10) * 10, Math.ceil(max / 9.5) * 10];
    }
}