import { Component, OnInit } from '@angular/core';

@Component({
    selector: 'app-customer-performance',
    template: `
        <div class="ui-g-12 boxShadow nav">
            <div class="ui-g-12 padding-0 top_items_box">
                <div class="ui-g-8 padding-0">
                    <span class="top_item" *ngFor="let car of cars ; let i = index ;" [ngClass]="{'active_item':showClass == car.flag}" [routerLink]="[car.link]"
                    (click)="showHideBrief1(car.flag)">
                        <span>{{car.title}}</span>
                    </span>
                    <span style="margin-left: 15px;color: red;">请设置任务目标值</span>
                </div>
                <div class="ui-g-4 padding-0 btn text-r">
                    业绩分析口径说明:
                    <span title="{{title1}}">
                        存款
                    </span>&nbsp;
                    <span title="{{title2}}">
                        贷款
                    </span>&nbsp;
                    <span title="{{title3}}">
                        FTP
                    </span>&nbsp;
                    <span title="{{title4}}">
                        不良
                    </span>
                </div>
            </div>
        </div>
        <div class="ui-g-12 boxShadow">
            <router-outlet></router-outlet>
        </div>
        <div id="returnTop" (click)="returnTop()">返回顶部</div>
    `,
    styles: [`
        #returnTop {
            background: #ffcb05;
            width: 40px;
            height: 40px;
            padding: 0.5em;
            line-height: 20px;
            text-align: center;
            position: fixed;
            bottom: 20px;
            right: 0;
            font-weight: 700;
            border-radius:10px;
            cursor: pointer;
        }
        .nav {
            margin-bottom: 10px;
        }
        .top_items_box {
            border-bottom: 2px solid #34CFE6;
        }
        .top_item {
            cursor: pointer;
            display: inline-block;
            width: 80px;
            height: 30px;
            line-height: 30px;
            text-align: center;
            font-weight: bold;
            font-size: 14px;
            outline: 0;
            border-top: 1px solid #e8e8e8;
            border-left: 1px solid #e8e8e8;
            border-right: 1px solid #e8e8e8;
            margin-left: 12px;
        }
        .active_item {
            margin-bottom: -2px;
            border-top: 2px solid #34CFE6;
            border-left: 2px solid #34CFE6;
            border-right: 2px solid #34CFE6;
            border-bottom: 2px solid #fafafa;
            color: #171819;
        }
        .padding-0 {
            padding: 0;
        }
        .text-r {
            text-align: right;
        }
        .btn {
            padding-right: 30px;
        }
        .btn span {
            padding: 5px 10px;
            border-radius: 15px;
            border: 1px solid #dadada;
            cursor: pointer;
        }
        .btn span:hover {
            background: #34CFE6;
            color: #fff;
        }
    `]
})

export class CustomerPerformanceComponent implements OnInit {

    cars: any[];
    showClass: number;//导航
    title1: string = '参考个人考核存款统计_对公与储蓄分离表加上外币存款。';
    title2: string = '参考营销人员贷款结构表，仅为贷款数据，不含信用卡、贴现、垫款。';
    title3: string = '参考FTP账户明细负债日报，FTP账户明细资产日报。';
    title4: string = '参考授信业务风险运行监测报表，不含信用卡数据，同时增加笔数的统计。';

    constructor(

    ) {

    }

    ngOnInit() {
        let url = window.location.href;
        if (url.indexOf('/pages/tzb/custom/performance-analysis/indicators-task-query') != -1) {
            this.showClass = 1;
        } else if (url.indexOf('/pages/tzb/custom/performance-analysis/indicators-task-setting') != -1) {
            this.showClass = 2;
        } else {
            this.showClass = 0;
        }

        this.cars = [
            {
                title: '业绩分析',
                link: '/pages/tzb/custom/performance-analysis/performance-home-pages',
                flag: 0
            },
            {
                title: '任务查询',
                link: '/pages/tzb/custom/performance-analysis/indicators-task-query',
                flag: 1
            },
            {
                title: '任务设置',
                link: '/pages/tzb/custom/performance-analysis/indicators-task-setting',
                flag: 2
            }
        ];
    }

    showHideBrief1(index) {
        this.showClass = index;
    }

    returnTop() {
        window.scrollTo(0, 0);
    }
}