    ngOnInit() {
        let urlParams;
        this.activatedRoute.params.subscribe(params => {
            urlParams = params;
        });
        // console.log(urlParams);
        if (urlParams && JSON.stringify(urlParams) != '{}') {
            if (urlParams.flag != '1') {
                this.queryAll(0, 1);
            }
        }
    }

    returnParams(ind, index) {//修改日/月
        this[`showIndex${ind}`] = index;
        if (index == 1) {
            this.dmFlag = 'D';
        } else {
            this.dmFlag = 'M';
        }
    }

    echoParams(ind, flag) {
        let index;
        if (flag == 'D') {
            index = 1;
        } else {
            index = 2;
        }
        this[`showIndex${ind}`] = index;
    }

    outValue(event) {
        let objTemp = this.customerPerformanceService.returnTime(event.queryCondition, event.index);
        if (event.index == 1) {
            objTemp = this.customerPerformanceService.returnTime(event.queryCondition, event.index);
        } else if (event.index == 2) {
            objTemp = event.queryCondition;
        }
        this.startDate = objTemp.startDate;
        this.endDate = objTemp.endDate;
        console.log('startDate:', this.startDate);
        console.log('endDate:', this.endDate);

        this.queryAll(0, 1);
    }

