import { Component, OnInit } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CustomerPerformanceHttpService } from 'app/pages/tzb/custom/http/customer-performance/customer-performance.http.service';
import { Message } from 'primeng/primeng';
import { CustomerPerformanceService } from '../../service/customer-performance.service';
import { ActivatedRoute } from '@angular/router';

@Component({
    selector: 'loan-industryInvestment',
    templateUrl: './loan-industryInvestment.component.html',
    styleUrls: ['./loan-industryInvestment.component.scss'],
    providers: [Commfunc]
})

export class LoanIndustryInvestmentComponent implements OnInit {

    commonHeader: any;
    msgs: Message[] = [];
    option1: any;
    option2: any;
    option3: any;
    option4: any;
    option5: any;
    option6: any;
    dmFlag: string;
    showIndex0: number;
    showIndex1: number = 1;
    showIndex2: number = 1;
    startDate: any = this.commfunc.transDateN(new Date().setTime(new Date().getTime() - 1000 * 60 * 60 * 24 * 30));
    endDate: any = this.commfunc.transDateN(new Date());
    pTooltip1: string = '截止到所选时间的最后一日，我的贷款余额在各行业的余额与占比';
    pTooltip2: string = '截止到所选时间的最后一日，部门合计贷款余额在各行业余额与占比';
    pTooltip3: string = '按日展示我的各行业每日贷款余额的变化趋势，按月展示自然月内各行业贷款日均变化趋势';
    pTooltip4: string = '截止到所选时间的最后一日，我的在贷贷款客户数在各行业的客户数与占比';
    pTooltip5: string = '截止到所选时间的最后一日，部门合计在贷贷款客户数在各行业的客户数与占比';
    pTooltip6: string = '按日展示我的各行业每日在贷贷款客户数的变化趋势，按月展示各行业在自然月持有贷款产品的客户数变化趋势';

    constructor(
        public commfunc: Commfunc,
        public customerPerformanceHttpService: CustomerPerformanceHttpService,
        public customerPerformanceService: CustomerPerformanceService,
        public activatedRoute: ActivatedRoute,
    ) {
        this.commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        //初值
        this.option1 = this.customerPerformanceService.returnOption2({ title: '我的各行业占比', name: '行业占比', data: [0] });
        this.option2 = this.customerPerformanceService.returnOption2({ title: '本级累计各行业占比', name: '行业占比', data: [0] });
        this.option3 = this.customerPerformanceService.returnOption({ title: '各行业变化趋势', legend: [], xAxisData: [], yAxisName: '贷款金额(万元)', series: [] });
        this.option4 = this.customerPerformanceService.returnOption2({ title: '我的各行业占比', name: '行业占比', data: [0] });
        this.option5 = this.customerPerformanceService.returnOption2({ title: '本级累计各行业占比', name: '行业占比', data: [0] });
        this.option6 = this.customerPerformanceService.returnOption({ title: '贷款金额占比', legend: [], xAxisData: [], yAxisName: '占比', series: [] });
    }

    ngOnInit() {
        let urlParams;
        this.activatedRoute.params.subscribe(params => {
            urlParams = params;
        });
        // console.log(urlParams);
        if (urlParams && JSON.stringify(urlParams) != '{}') {
            if (urlParams.flag != '1') {
                this.loansIndtstryQuery(1, 1);
                this.loansIndtstryQueryCust(2, 1);
            }
        }
    }

    outValue(event) {
        let objTemp;
        if (event.index == 1) {
            objTemp = this.customerPerformanceService.returnTime(event.queryCondition, event.index);
        } else if (event.index == 2) {
            objTemp = event.queryCondition;
        }
        this.startDate = objTemp.startDate;
        this.endDate = objTemp.endDate;
        console.log('startDate:', this.startDate);
        console.log('endDate:', this.endDate);
        this.loansIndtstryQuery(1, 1);
        this.loansIndtstryQueryCust(2, 1);
    }

    returnParams(ind, index) {
        this[`showIndex${ind}`] = index;
        if (index == 1) {
            this.dmFlag = 'D';
        } else {
            this.dmFlag = 'M';
        }
    }

    echoParams(ind, flag) {
        let index;
        if (flag == 'D') {
            index = 1;
        } else {
            index = 2;
        }
        this[`showIndex${ind}`] = index;
    }

    loansIndtstryQuery(ind, index) {//贷款行业投向
        this.returnParams(ind, index);
        let params = {
            tellerId: this.commonHeader.userId,
            dmFlag: this.dmFlag,
            startDate: this.startDate,
            endDate: this.endDate
        }
        this.customerPerformanceHttpService.loansIndtstryQuery(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let xData;
                let ydata = [];
                let data1 = [];
                let data2 = [];
                let ydata2 = [];
                let series = [];
                if (data.myAllIndtstryAmt && JSON.stringify(data.myAllIndtstryAmt) != '{}' && data.departmentAllIndtstryAmt && JSON.stringify(data.departmentAllIndtstryAmt) != '{}') {
                    for (let k in data.myAllIndtstryAmt) {
                        let objTemp = {};
                        objTemp['name'] = k;
                        objTemp['value'] = data.myAllIndtstryAmt[k];
                        data1.push(objTemp);
                    }
                    for (let k in data.departmentAllIndtstryAmt) {
                        let objTemp = {};
                        objTemp['name'] = k;
                        objTemp['value'] = data.departmentAllIndtstryAmt[k];
                        data2.push(objTemp);
                    }
                }
                if (data.loansIndtstryDayMap && JSON.stringify(data.loansIndtstryDayMap) != '{}') {
                    for (let list in data.loansIndtstryDayMap) {
                        let arrTemp = [];
                        if (data.loansIndtstryDayMap[list] && data.loansIndtstryDayMap[list].length != 0) {
                            xData = [];
                            data.loansIndtstryDayMap[list].forEach((element, i) => {
                                let arr = element.dt.split('-');
                                if (data.dmFlag && data.dmFlag == 'D') {
                                    xData.push(`${arr[0]}年${arr[1]}月${arr[2]}日`);
                                } else if (data.dmFlag && data.dmFlag == 'M') {
                                    xData.push(`${arr[0]}年${arr[1]}月`);
                                }
                                arrTemp.push(element.dkje_w);
                            });
                            let objTemp1 = {};
                            objTemp1['name'] = data.loansIndtstryDayMap[list][0][list];
                            ydata.push(objTemp1);
                            series.push(
                                {
                                    name: data.loansIndtstryDayMap[list][0][list],
                                    data: arrTemp,
                                    type: 'line',
                                    itemStyle: {
                                        normal: {
                                            label: {
                                                show: true,
                                                formatter: (params) => {
                                                    if (params.dataIndex == 0 || params.dataIndex == arrTemp.length - 1) {
                                                        return params.value
                                                    } else {
                                                        return ''
                                                    }
                                                }
                                            }
                                        }
                                    }
                                },
                            )
                        }
                    }
                }
                this.echoParams(1, data.dmFlag);
                this.option1 = {
                    title: {
                        text: '我的各行业占比',
                        left: 'center',
                        y: '10px',
                        textStyle: {
                            fontWeight: '500',
                            fontSize: '16'
                        }
                    },
                    // color: ['#37A2DA', '#FB7293', '#9FE6B8', '#E7BCF3', '#67E0E3', '#FFDB5C'],
                    series: [
                        {
                            name: '行业占比',
                            data: data1,
                            type: 'pie',
                            radius: ['50%', '70%'],
                            label: {
                                normal: {
                                    formatter: '{b} {c} , {d}%'
                                }
                            }
                        }
                    ]
                }
                this.option2 = {
                    title: {
                        text: '本级累计各行业占比',
                        left: 'center',
                        y: '10px',
                        textStyle: {
                            fontWeight: '500',
                            fontSize: '16'
                        }
                    },
                    series: [
                        {
                            name: '行业占比',
                            data: data2,
                            type: 'pie',
                            radius: ['50%', '70%'],
                            label: {
                                normal: {
                                    formatter: '{b} {c} , {d}%'
                                }
                            }
                        }
                    ]
                }

                this.option3 = {
                    title: {
                        text: '各行业变化趋势',
                        left: 'center',
                        y: '10px',
                        textStyle: {
                            fontWeight: '500',
                            fontSize: '16'
                        }
                    },
                    legend: {
                        top: 'bottom',
                        itemGap: 20,
                        icon: 'line',
                        textStyle: {
                            color: '#34CFE6'
                        }
                    },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'cross'
                        }
                    },
                    grid: {
                        width: '80%',
                        height: '75%',
                        left: '10%',
                        bottom: '10%',
                        containLabel: true
                    },
                    xAxis: {
                        type: 'category',
                        data: xData,
                        axisLabel: {
                            rotate: 30
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            show: false
                        }
                    },
                    yAxis: {
                        type: 'value',
                        name: '贷款金额(万元)',
                        nameGap: 30,
                        scale: true,
                        splitArea: {
                            show: true
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            show: false
                        }
                    },
                    series: series
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }

    loansIndtstryQueryCust(ind, index) {//贷款行业-客户数
        this.returnParams(ind, index);
        let params = {
            tellerId: this.commonHeader.userId,
            dmFlag: this.dmFlag,
            startDate: this.startDate,
            endDate: this.endDate
        }
        this.customerPerformanceHttpService.loansIndtstryQueryCust(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let xData;
                let ydata = [];
                let data1 = [];
                let data2 = [];
                let ydata2 = [];
                let series = [];
                if (data.myAllIndtstryAmt && JSON.stringify(data.myAllIndtstryAmt) != '{}' && data.departmentAllIndtstryAmt && JSON.stringify(data.departmentAllIndtstryAmt) != '{}') {
                    for (let k in data.myAllIndtstryAmt) {
                        let objTemp = {};
                        objTemp['name'] = k;
                        objTemp['value'] = data.myAllIndtstryAmt[k];
                        data1.push(objTemp);
                    }
                    for (let k in data.departmentAllIndtstryAmt) {
                        let objTemp = {};
                        objTemp['name'] = k;
                        objTemp['value'] = data.departmentAllIndtstryAmt[k];
                        data2.push(objTemp);
                    }
                }
                if (data.loansIndtstryDayMap && JSON.stringify(data.loansIndtstryDayMap) != '{}') {
                    for (let list in data.loansIndtstryDayMap) {
                        let arrTemp = [];
                        if (data.loansIndtstryDayMap[list] && data.loansIndtstryDayMap[list].length != 0) {
                            xData = [];
                            data.loansIndtstryDayMap[list].forEach((element, i) => {
                                let arr = element.dt.split('-');
                                if (data.dmFlag && data.dmFlag == 'D') {
                                    xData.push(`${arr[0]}年${arr[1]}月${arr[2]}日`);
                                } else if (data.dmFlag && data.dmFlag == 'M') {
                                    xData.push(`${arr[0]}年${arr[1]}月`);
                                }
                                arrTemp.push(element.dkkh_w);
                            });
                            let objTemp1 = {};
                            objTemp1['name'] = data.loansIndtstryDayMap[list][0][list];
                            ydata.push(objTemp1);
                            series.push(
                                {
                                    name: data.loansIndtstryDayMap[list][0][list],
                                    data: arrTemp,
                                    type: 'line',
                                    itemStyle: {
                                        normal: {
                                            label: {
                                                show: true,
                                                formatter: (params) => {
                                                    if (params.dataIndex == 0 || params.dataIndex == arrTemp.length - 1) {
                                                        return params.value
                                                    } else {
                                                        return ''
                                                    }
                                                }
                                            }
                                        }
                                    }
                                },
                            )
                        }
                    }
                }
                this.echoParams(2, data.dmFlag);
                this.option4 = {
                    title: {
                        text: '我的各行业占比',
                        left: 'center',
                        y: '10px',
                        textStyle: {
                            fontWeight: '500',
                            fontSize: '16'
                        }
                    },
                    series: [
                        {
                            name: '行业占比',
                            data: data1,
                            type: 'pie',
                            radius: ['50%', '70%'],
                            center: ['50%', '55%'],
                            label: {
                                normal: {
                                    formatter: '{b} {c} , {d}%'
                                }
                            }
                        }
                    ]
                }
                this.option5 = {
                    title: {
                        text: '本级累计各行业占比',
                        left: 'center',
                        y: '10px',
                        textStyle: {
                            fontWeight: '500',
                            fontSize: '16'
                        }
                    },
                    series: [
                        {
                            name: '行业占比',
                            data: data2,
                            type: 'pie',
                            radius: ['50%', '70%'],
                            center: ['50%', '55%'],
                            label: {
                                normal: {
                                    formatter: '{b} {c} , {d}%'
                                }
                            }
                        }
                    ]
                }

                this.option6 = {
                    title: {
                        text: '各行业变化趋势',
                        left: 'center',
                        y: '10px',
                        textStyle: {
                            fontWeight: '500',
                            fontSize: '16'
                        }
                    },
                    legend: {
                        top: 'bottom',
                        itemGap: 20,
                        icon: 'line',
                        textStyle: {
                            color: '#34CFE6'
                        }
                    },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'cross'
                        }
                    },
                    grid: {
                        width: '80%',
                        height: '75%',
                        left: '10%',
                        bottom: '10%',
                        containLabel: true
                    },
                    xAxis: {
                        type: 'category',
                        data: xData,
                        axisLabel: {
                            rotate: 30
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            show: false
                        }
                    },
                    yAxis: {
                        type: 'value',
                        name: '贷款客户数',
                        nameGap: 30,
                        scale: true,
                        splitArea: {
                            show: true
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            show: false
                        }
                    },
                    series: series
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
}