import { Component, OnInit } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CustomerPerformanceHttpService } from 'app/pages/tzb/custom/http/customer-performance/customer-performance.http.service';
import { Message } from 'primeng/primeng';
import { CustomerPerformanceService } from '../../service/customer-performance.service';
import { ActivatedRoute } from '@angular/router';

@Component({
    selector: 'loan-increasementalTendency',
    templateUrl: './loan-increasementalTendency.component.html',
    styleUrls: ['./loan-increasementalTendency.component.scss'],
    providers: [Commfunc]
})

export class LoanIncreasementalTendencyComponent implements OnInit {

    startDate: any = this.commfunc.transDateN(new Date().setTime(new Date().getTime() - 1000 * 60 * 60 * 24 * 30));
    endDate: any = this.commfunc.transDateN(new Date());
    option1: any;
    option2: any;
    option3: any;
    option4: any;
    showIndex1: number = 1;
    showIndex2: number = 1;
    showIndex3: number = 1;
    showIndex4: number = 1;
    msgs: Message[] = [];
    dmFlag: string;
    commonHeader: any;
    pTooltip1: string = `我：按日展示自然月新增客户截止到当日所带来的贷款余额，按月展示自然月新增客户的贷款日均\n部门平均：按日展示每日部门合计新增客户带来的贷款余额/部门总营销人数，按月则日均同理`;
    pTooltip2: string = `我：按日展示自然月新增客户截止到当日所带来的贷款余额在部门合计新增客户贷款余额占比，按月展示自然月新增客户的贷款日均在部门合计新增客户贷款日均占比\n部门平均：1/部门总营销人数，计算部门内人均占比\n部门第一名：按日展示部门中新增客户贷款余额最高的客户经理对应的贷款余额在部门合计新增客户贷款余额中占比，按月则日均同理`;
    pTooltip3: string = `我：按日展示自然月截止到每日新增贷款客户数，按月展示自然月内新增持有贷款的客户数\n部门平均：按日展示自然月部门内合计截止到每日新增贷款客户数/部门总营销人数，按月同理`;

    constructor(
        public commfunc: Commfunc,
        public customerPerformanceHttpService: CustomerPerformanceHttpService,
        public customerPerformanceService: CustomerPerformanceService,
        public activatedRoute: ActivatedRoute,
    ) {
        this.commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        //初值
        this.option1 = this.customerPerformanceService.returnOption({ title: '新增客户贷款金额', legend: [{ name: '我' }, { name: '部门平均' }], xAxisData: [], yAxisName: '金额(万元)', series: [] });
        this.option2 = this.customerPerformanceService.returnOption({ title: '新增客户贷款金额占比', legend: [{ name: '我' }, { name: '部门平均' }], xAxisData: [], yAxisName: '占比', series: [] });
        this.option3 = this.customerPerformanceService.returnOption({ title: '增量客户数', legend: [{ name: '我' }, { name: '部门平均' }], xAxisData: [], yAxisName: '客户数', series: [] });
    }

    ngOnInit() {
        let urlParams;
        this.activatedRoute.params.subscribe(params => {
            urlParams = params;
        });
        // console.log(urlParams);
        if (urlParams && JSON.stringify(urlParams) != '{}') {
            if (urlParams.flag != '1') {
                this.newLoansAmtQuery(1, 1);
                this.newLoansAmtProQuery(2, 1);
                this.newLoansCustQuery(3, 1);
            }
        }
    }

    outValue(event) {
        let objTemp;
        if (event.index == 1) {
            objTemp = this.customerPerformanceService.returnTime(event.queryCondition, event.index);
        } else if (event.index == 2) {
            objTemp = event.queryCondition;
        }
        this.startDate = objTemp.startDate;
        this.endDate = objTemp.endDate;
        console.log('startDate:', this.startDate);
        console.log('endDate:', this.endDate);
        this.newLoansAmtQuery(1, 1);
        this.newLoansAmtProQuery(2, 1);
        this.newLoansCustQuery(3, 1);
    }

    returnParams(ind, index) {
        this[`showIndex${ind}`] = index;
        if (index == 1) {
            this.dmFlag = 'D';
        } else {
            this.dmFlag = 'M';
        }
    }

    echoParams(ind, flag) {
        let index;
        if (flag == 'D') {
            index = 1;
        } else {
            index = 2;
        }
        this[`showIndex${ind}`] = index;
    }

    newLoansAmtQuery(ind, index) {
        this.returnParams(ind, index);
        let params = {
            tellerId: this.commonHeader.userId,
            dmFlag: this.dmFlag,
            startDate: this.startDate,
            endDate: this.endDate
        }
        this.customerPerformanceHttpService.newLoansAmtQuery(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let xData = [];
                let yData1 = [];
                let yData2 = [];
                let yData3 = [];
                if (data.myNewdLoansList && data.myNewdLoansList.length != 0 && data.departmentNewLoansList && data.departmentNewLoansList.length != 0 && data.brachNewLoansList && data.brachNewLoansList.length != 0) {
                    data.myNewdLoansList.forEach(element => {
                        let arr = element.dt.split('-');
                        if (data.dmFlag && data.dmFlag == 'D') {
                            xData.push(`${arr[0]}年${arr[1]}月${arr[2]}日`);
                        } else if (data.dmFlag && data.dmFlag == 'M') {
                            xData.push(`${arr[0]}年${arr[1]}月`);
                        }
                        yData1.push(element.dkje_w);
                    });
                    data.departmentNewLoansList.forEach(element => {
                        yData2.push(element.dkje_bm);
                    });
                    data.brachNewLoansList.forEach(element => {
                        yData3.push(element.dkje_fh);
                    });
                }
                this.echoParams(1, data.dmFlag);
                this.option1 = {
                    title: {
                        text: '新增客户贷款金额',
                        left: 'center',
                        y: '10px',
                        textStyle: {
                            fontWeight: '500',
                            fontSize: '16'
                        }
                    },
                    legend: {
                        data: [{ name: '我' }, { name: '部门平均' }],
                        top: 'bottom',
                        itemGap: 20,
                        icon: 'line',
                        textStyle: {
                            color: '#34CFE6'
                        }
                    },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'cross'
                        }
                    },
                    grid: {
                        width: '80%',
                        height: '75%',
                        left: '10%',
                        bottom: '10%',
                        containLabel: true
                    },
                    xAxis: {
                        type: 'category',
                        data: xData,
                        axisLabel: {
                            rotate: 30
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            show: false
                        }
                    },
                    yAxis: {
                        type: 'value',
                        name: '金额(万元)',
                        nameGap: 30,
                        scale: true,
                        splitArea: {
                            show: true
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            show: false
                        }
                    },
                    series: [
                        {
                            name: '我',
                            data: yData1,
                            type: 'line',
                            itemStyle: {
                                normal: {
                                    label: {
                                        show: true,
                                        formatter: (params) => {
                                            if (params.dataIndex == 0 || params.dataIndex == yData1.length - 1) {
                                                return params.value
                                            } else {
                                                return ''
                                            }
                                        }
                                    }
                                }
                            }
                        },
                        {
                            name: '部门平均',
                            data: yData2,
                            type: 'line',
                            itemStyle: {
                                normal: {
                                    lineStyle: {
                                        type: 'dashed'
                                    },
                                    label: {
                                        show: true,
                                        formatter: (params) => {
                                            if (params.dataIndex == 0 || params.dataIndex == yData2.length - 1) {
                                                return params.value
                                            } else {
                                                return ''
                                            }
                                        }
                                    }
                                }
                            }
                        },
                        // {
                        //     name: '分行优秀线',
                        //     data: yData3,
                        //     type: 'line',
                        //     itemStyle: {
                        //         normal: {
                        //             lineStyle: {
                        //                 type: 'dashed'
                        //             },
                        //             label: {
                        //                 show: true,
                        //                 formatter: (params) => {
                        //                     if (params.dataIndex == 0 || params.dataIndex == yData3.length - 1) {
                        //                         return params.value
                        //                     } else {
                        //                         return ''
                        //                     }
                        //                 }
                        //             }
                        //         }
                        //     }
                        // },
                    ]
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }

    newLoansAmtProQuery(ind, index) {
        this.returnParams(ind, index);
        let params = {
            tellerId: this.commonHeader.userId,
            dmFlag: this.dmFlag,
            startDate: this.startDate,
            endDate: this.endDate
        }
        this.customerPerformanceHttpService.newLoansAmtProQuery(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let xData = [];
                let yData1 = [];
                let yData2 = [];
                let yData3 = [];
                if (data.myNewLoansProList && data.myNewLoansProList.length != 0 && data.departmentNewLoansProList && data.departmentNewLoansProList.length != 0 && data.departmentOneNewLoansProList && data.departmentOneNewLoansProList.length != 0) {
                    data.myNewLoansProList.forEach(element => {
                        let arr = element.dt.split('-');
                        if (data.dmFlag && data.dmFlag == 'D') {
                            xData.push(`${arr[0]}年${arr[1]}月${arr[2]}日`);
                        } else if (data.dmFlag && data.dmFlag == 'M') {
                            xData.push(`${arr[0]}年${arr[1]}月`);
                        }
                        yData1.push(Number(element.dkjezb_w * 100).toFixed(2));
                    });
                    data.departmentNewLoansProList.forEach(element => {
                        yData2.push(Number(element.dkjezb_bm * 100).toFixed(2));
                    });
                    data.departmentOneNewLoansProList.forEach(element => {
                        yData3.push(Number(element.dkjezb_dy * 100).toFixed(2));
                    });
                }
                this.echoParams(2, data.dmFlag);
                this.option2 = {
                    title: {
                        text: '新增客户贷款金额占比',
                        left: 'center',
                        y: '10px',
                        textStyle: {
                            fontWeight: '500',
                            fontSize: '16'
                        }
                    },
                    legend: {
                        data: [{ name: '我' }, { name: '部门平均' }, { name: '部门第一名' }],
                        top: 'bottom',
                        itemGap: 20,
                        icon: 'line',
                        textStyle: {
                            color: '#34CFE6'
                        }
                    },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'cross'
                        }
                    },
                    grid: {
                        width: '80%',
                        height: '75%',
                        left: '10%',
                        bottom: '10%',
                        containLabel: true
                    },
                    xAxis: {
                        type: 'category',
                        data: xData,
                        axisLabel: {
                            rotate: 30
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            show: false
                        }
                    },
                    yAxis: {
                        type: 'value',
                        name: '占比',
                        nameGap: 30,
                        scale: true,
                        axisLabel: {
                            formatter: '{value}%'
                        },
                        splitArea: {
                            show: true
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            show: false
                        }
                    },
                    series: [
                        {
                            name: '我',
                            data: yData1,
                            type: 'line',
                            itemStyle: {
                                normal: {
                                    label: {
                                        show: true,
                                        formatter: (params) => {
                                            if (params.dataIndex == 0 || params.dataIndex == yData1.length - 1) {
                                                return params.value
                                            } else {
                                                return ''
                                            }
                                        }
                                    }
                                }
                            }
                        },
                        {
                            name: '部门平均',
                            data: yData2,
                            type: 'line',
                            itemStyle: {
                                normal: {
                                    lineStyle: {
                                        type: 'dashed'
                                    },
                                    label: {
                                        show: true,
                                        formatter: (params) => {
                                            if (params.dataIndex == 0 || params.dataIndex == yData2.length - 1) {
                                                return params.value
                                            } else {
                                                return ''
                                            }
                                        }
                                    }
                                }
                            }
                        },
                        {
                            name: '部门第一名',
                            data: yData3,
                            type: 'line',
                            itemStyle: {
                                normal: {
                                    lineStyle: {
                                        type: 'dashed'
                                    },
                                    label: {
                                        show: true,
                                        formatter: (params) => {
                                            if (params.dataIndex == 0 || params.dataIndex == yData3.length - 1) {
                                                return params.value
                                            } else {
                                                return ''
                                            }
                                        }
                                    }
                                }
                            }
                        },
                    ]
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }

    newLoansCustQuery(ind, index) {
        this.returnParams(ind, index);
        let params = {
            tellerId: this.commonHeader.userId,
            dmFlag: this.dmFlag,
            startDate: this.startDate,
            endDate: this.endDate
        }
        this.customerPerformanceHttpService.newLoansCustQuery(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let xData = [];
                let yData1 = [];
                let yData2 = [];
                let yData3 = [];
                if (data.brachNewCustList && data.brachNewCustList.length != 0 && data.departmentNewCustList && data.departmentNewCustList.length != 0 && data.myNewCustList && data.myNewCustList.length != 0) {
                    data.myNewCustList.forEach(element => {
                        let arr = element.dt.split('-');
                        if (data.dmFlag && data.dmFlag == 'D') {
                            xData.push(`${arr[0]}年${arr[1]}月${arr[2]}日`);
                        } else if (data.dmFlag && data.dmFlag == 'M') {
                            xData.push(`${arr[0]}年${arr[1]}月`);
                        }
                        yData1.push(element.dkkh_w);
                    });
                    data.departmentNewCustList.forEach(element => {
                        yData2.push(element.dkkh_bm);
                    });
                    data.brachNewCustList.forEach(element => {
                        yData3.push(element.dkkh_fh);
                    });
                }
                this.echoParams(3, data.dmFlag);
                this.option3 = {
                    title: {
                        text: '增量客户数',
                        left: 'center',
                        y: '10px',
                        textStyle: {
                            fontWeight: '500',
                            fontSize: '16'
                        }
                    },
                    legend: {
                        data: [{ name: '我' }, { name: '部门平均' }],
                        top: 'bottom',
                        itemGap: 20,
                        icon: 'line',
                        textStyle: {
                            color: '#34CFE6'
                        }
                    },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'cross'
                        }
                    },
                    grid: {
                        width: '80%',
                        height: '75%',
                        left: '10%',
                        bottom: '10%',
                        containLabel: true
                    },
                    xAxis: {
                        type: 'category',
                        data: xData,
                        axisLabel: {
                            rotate: 30
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            show: false
                        }
                    },
                    yAxis: {
                        type: 'value',
                        name: '客户数',
                        nameGap: 30,
                        scale: true,
                        splitArea: {
                            show: true
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            show: false
                        }
                    },
                    series: [
                        {
                            name: '我',
                            data: yData1,
                            type: 'line',
                            itemStyle: {
                                normal: {
                                    label: {
                                        show: true,
                                        formatter: (params) => {
                                            if (params.dataIndex == 0 || params.dataIndex == yData1.length - 1) {
                                                return params.value
                                            } else {
                                                return ''
                                            }
                                        }
                                    }
                                }
                            }
                        },
                        {
                            name: '部门平均',
                            data: yData2,
                            type: 'line',
                            itemStyle: {
                                normal: {
                                    lineStyle: {
                                        type: 'dashed'
                                    },
                                    label: {
                                        show: true,
                                        formatter: (params) => {
                                            if (params.dataIndex == 0 || params.dataIndex == yData2.length - 1) {
                                                return params.value
                                            } else {
                                                return ''
                                            }
                                        }
                                    }
                                }
                            }
                        },
                        // {
                        //     name: '分行优秀线',
                        //     data: yData3,
                        //     type: 'line',
                        //     itemStyle: {
                        //         normal: {
                        //             lineStyle: {
                        //                 type: 'dashed'
                        //             },
                        //             label: {
                        //                 show: true,
                        //                 formatter: (params) => {
                        //                     if (params.dataIndex == 0 || params.dataIndex == yData3.length - 1) {
                        //                         return params.value
                        //                     } else {
                        //                         return ''
                        //                     }
                        //                 }
                        //             }
                        //         }
                        //     }
                        // },
                    ]
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
}