    indicatorProgressQuery(ind, index) {//进度查询
        this.returnParams2(ind, index);
        // let startDate, endDate;
        // if (this.startDate) {
        //     startDate = `${this.startDate.split('-')[0]}-${this.startDate.split('-')[1]}`;
        // }
        // if (this.endDate) {
        //     endDate = `${this.endDate.split('-')[0]}-${this.endDate.split('-')[1]}`;
        // }

        let params = {
            tellerId: this.commonHeader.userId,
            nyFlag: this.nyFlag,
            startDate: this.startDate,
            endDate: this.endDate
        }
        this.customerPerformanceHttpService.indicatorProgressQuery(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let je1 = data.depositAmt ? data.depositAmt : '0.00';
                let je2 = this.depositAmt ? this.depositAmt : '0.00';
                // if (je2 != '0') {
                //     this.collection[0]['zb'] = `${(parseFloat(je1) / parseFloat(je2) * 100).toFixed(2)}%`;
                // } else {
                //     this.collection[0]['zb'] = '0.00%';
                // }
                this.collection[0]['zb'] = `${data.depositPlan ? Number(data.depositPlan * 100).toFixed(2) : '0.00'}%`;
                this.collection[0]['je1'] = je1 + '万元';
                this.collection[0]['je2'] = je2 + '万元';

                let je3 = data.loansAmt ? data.loansAmt : '0.00';
                let je4 = this.loanAmt ? this.loanAmt : '0.00';
                // if (je4 != '0') {
                //     this.collection[1]['zb'] = `${(parseFloat(je3) / parseFloat(je4) * 100).toFixed(2)}%`;
                // } else {
                //     this.collection[1]['zb'] = '0.00%';
                // }
                this.collection[1]['zb'] = `${data.loansPlan ? Number(data.loansPlan * 100).toFixed(2) : '0.00'}%`;
                this.collection[1]['je1'] = je3 + '万元';
                this.collection[1]['je2'] = je4 + '万元';

                let je5 = data.ftpAmt ? data.ftpAmt : '0.00';
                let je6 = this.ftpAmt ? String(parseFloat(this.ftpAmt) / 10000) : '0.00';
                // if (je6 != '0') {
                //     this.collection[2]['zb'] = `${(parseFloat(je5) / parseFloat(je6) * 100).toFixed(2)}%`;
                // } else {
                //     this.collection[2]['zb'] = '0.00%';
                // }
                this.collection[2]['zb'] = `${data.ftpPlan ? Number(data.ftpPlan * 100).toFixed(2) : '0.00'}%`;
                this.collection[2]['je1'] = je5 + '万元';
                this.collection[2]['je2'] = je6 + '万元';

                let je7 = data.badness ? data.badness : '0.00';
                let je8 = this.badnessAmt ? this.badnessAmt : '0.00';
                this.collection[3]['je2'] = (parseFloat(je8)).toFixed(2) + '%';
                this.collection[3]['zb'] = parseFloat(je7).toFixed(2) + '%';
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }

    taskArrangementQuery(ind, index) {//指标查询
        let endDate;
        if (ind && ind == -2) {
            endDate = '00';
        } else {
            if (this.endDate) {
                endDate = `${this.endDate.split('-')[1]}`;
            }
        }
        let params = {
            tellerId: this.commonHeader.userId,
            orgId: this.commonHeader.orgId,
            yearDate: String(new Date().getFullYear()),
            monthDate: endDate,
            taskType: ""
        }
        this.customerPerformanceHttpService.taskArrangementQuery(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.taskArrangementList && data.taskArrangementList.length != 0) {
                    data.taskArrangementList.forEach(element => {
                        switch (element.signType) {
                            case "1": this.depositAmt = element.taskAmt; break;
                            case "2": this.loanAmt = element.taskAmt; break;
                            case "3": this.ftpAmt = element.taskAmt; break;
                            case "4": this.badnessAmt = element.taskAmt; break;
                        }
                    });
                }
                this.indicatorProgressQuery(0, index);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }

