                        this.ftpAmt1 = this.ftpAmt1 ? this.ftpAmt1 : '0.00';
                        this.badnessAmt1 = this.badnessAmt1 ? this.badnessAmt1 : '0.00';
                        this.indicatorProgressGroessQuery(-1, 1);
