import { Component, OnInit } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CustomerPerformanceHttpService } from 'app/pages/tzb/custom/http/customer-performance/customer-performance.http.service';
import { Message } from 'primeng/primeng';
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { MessageService } from "primeng/components/common/messageservice";
import { CustomerPerformanceService } from "app/pages/tzb/custom/customer-performance/service/customer-performance.service";
import { ActivatedRoute } from '@angular/router';

@Component({
    selector: 'app-badness-propertyDistribution',
    templateUrl: './badness-propertyDistribution.component.html',
    styleUrls: ['./badness-propertyDistribution.component.scss'],
    providers: [Commfunc]
})

export class BadnessPropertyDistributionComponent implements OnInit {

    commonHeader: any;
    loadingShow: any = { flag: false }; // loading框
    msgs: Message[] = [];

    startDate: any = this.commfunc.transDateN(new Date().setTime(new Date().getTime() - 1000 * 60 * 60 * 24 * 30));
    endDate: any = this.commfunc.transDateN(new Date());

    option1: any;   // 行业-新不良笔数
    option2: any;   // 行业-新不良金额
    option3: any;   // 产品-新不良笔数
    option4: any;   // 产品-新不良金额
    option5: any;   // 担保方式-新不良笔数
    option6: any;   // 担保方式-新不良金额
    option7: any;   // 客户年龄分布-新不良笔数
    option8: any;   // 客户年龄分布-新不良金额

    pTooltip1: string = '截止到所选时间的最后一日，我的不良贷款发生在各行业的笔数与占比';
    pTooltip2: string = '截止到所选时间的最后一日，我的不良贷款发生在各行业的余额与占比';
    pTooltip3: string = '截止到所选时间的最后一日，我的不良贷款发生在各产品的笔数与占比';
    pTooltip4: string = '截止到所选时间的最后一日，我的不良贷款发生在各产品的余额与占比';
    pTooltip5: string = '截止到所选时间的最后一日，我的不良贷款发生在各担保方式的笔数与占比';
    pTooltip6: string = '截止到所选时间的最后一日，我的不良贷款发生在各担保方式的余额与占比';
    pTooltip7: string = '截止到所选时间的最后一日，我的不良贷款发生在不同年龄段客户的笔数与占比';
    pTooltip8: string = '截止到所选时间的最后一日，我的不良贷款发生在不同年龄段客户的余额与占比';

    industryList: any[] = [//行业
        { value: 'A', label: '农、林、牧、渔业' },
        { value: 'B', label: '采矿业' },
        { value: 'C', label: '制造业' },
        { value: 'D', label: '电力、热力、燃气及水生产和供应业' },
        { value: 'E', label: '建筑业' },
        { value: 'F', label: '批发和零售业' },
        { value: 'G', label: '交通运输、仓储和邮政业' },
        { value: 'H', label: '住宿和餐饮业' },
        { value: 'I', label: '信息传输、软件和信息技术服务业' },
        { value: 'J', label: '金融业' },
        { value: 'K', label: '房地产业' },
        { value: 'L', label: '租赁和商务服务业' },
        { value: 'M', label: '科学研究和技术服务业' },
        { value: 'N', label: '水利、环境和公共设施管理业' },
        { value: 'O', label: '居民服务、修理和其他服务业' },
        { value: 'P', label: '教育' },
        { value: 'Q', label: '卫生和社会工作' },
        { value: 'R', label: '文化、体育和娱乐业' },
        { value: 'S', label: '公共管理、社会保障和社会组织' },
        { value: 'T', label: '国际组织' },
        { value: 'Z', label: '其他行业' }
    ];
    ageList: any[] = [//年龄
        { value: '1', label: '25岁以下' },
        { value: '2', label: '25岁-35岁' },
        { value: '3', label: '35岁-45岁' },
        { value: '4', label: '45岁-55岁' },
        { value: '5', label: '55岁-65岁' },
        { value: '6', label: '65岁以上' }
    ];
    productList: any[] = [//产品
        { value: '01', label: '一般贷款 ' },
        { value: '02', label: '小本贷款 ' },
        { value: '03', label: '消费贷款 ' },
        { value: '04', label: '贸易融资 ' },
        { value: '05', label: '贴现 ' },
        { value: '06', label: '时贷通 ' },
        { value: '09', label: '自助卡' }
    ];
    guaranteeList: any[] = [//担保方式
        { value: '1201', label: '抵押' },
        { value: '1301', label: '质押' },
        { value: '1302', label: '存单质押' },
        { value: '1101', label: '保证' },
        { value: '1139', label: '保证金' },
        { value: '-1', label: '未知' },
        { value: '1001', label: '无' }
    ]

    constructor(
        public httpService: CustomerPerformanceHttpService,
        public commfunc: Commfunc,
        private commonFunc: Customtools,
        private messageService: MessageService,
        public customerPerformanceService: CustomerPerformanceService,
        public activatedRoute: ActivatedRoute,
    ) {
        this.commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        //初值
        this.option1 = this.customerPerformanceService.returnOption2({ title: '行业-新不良笔数', name: '行业', data: [0] });
        this.option2 = this.customerPerformanceService.returnOption2({ title: '行业-新不良金额', name: '行业', data: [0] });
        this.option3 = this.customerPerformanceService.returnOption2({ title: '产品-新不良笔数', name: '产品', data: [0] });
        this.option4 = this.customerPerformanceService.returnOption2({ title: '产品-新不良金额', name: '产品', data: [0] });
        this.option5 = this.customerPerformanceService.returnOption2({ title: '担保方式-新不良笔数', name: '担保方式', data: [0] });
        this.option6 = this.customerPerformanceService.returnOption2({ title: '担保方式-新不良金额', name: '担保方式', data: [0] });
        this.option7 = this.customerPerformanceService.returnOption3({ title: '新不良笔数', xAxisName: '年龄', yAxisName: '不良笔数', series: [] });
        this.option8 = this.customerPerformanceService.returnOption3({ title: '新不良金额', xAxisName: '年龄', yAxisName: '不良金额（万元）', series: [] });
    }

    ngOnInit() {
        let urlParams;
        this.activatedRoute.params.subscribe(params => {
            urlParams = params;
        });
        // console.log(urlParams);
        if (urlParams && JSON.stringify(urlParams) != '{}') {
            if (urlParams.flag != '1') {
                this.queryAll('CP');
                this.queryAll('HY');
                this.queryAll('DB');
                this.queryAll('NL');
            }
        }
    }

    outValue(event) {
        let objTemp = this.customerPerformanceService.returnTime(event.queryCondition, event.index);
        if (event.index == 1) {
            objTemp = this.customerPerformanceService.returnTime(event.queryCondition, event.index);
        } else if (event.index == 2) {
            objTemp = event.queryCondition;
        }
        this.startDate = objTemp.startDate;
        this.endDate = objTemp.endDate;
        console.log('startDate:', this.startDate);
        console.log('endDate:', this.endDate);

        this.queryAll('CP');
        this.queryAll('HY');
        this.queryAll('DB');
        this.queryAll('NL');
    }


    queryAll(badnessType) {
        const params = {
            tellerId: this.commonHeader.userId,
            startDate: this.startDate,
            endDate: this.endDate,
            badnessType: badnessType
        }
        const configParam = {
            httpService: this.httpService,
            loadingFlag: true,
            messageFlag: false,
            inputParam: params,
            message: this.messageService,
            injectName: 'badnessDistributeQuery',
            loadingShow: this.loadingShow,
            messageInfo: '',
        }
        this.commonFunc.haddleData(configParam, (bodyData) => {
            let data1 = [];
            let data2 = [];
            let data3 = [];
            let data4 = [];
            let data5 = [];
            let data6 = [];
            if (bodyData.disNumList && bodyData.disNumList.length > 0) {
                bodyData.disNumList.forEach(el => {
                    let dataObj = {};
                    dataObj['name'] = el.key;
                    dataObj['value'] = el.disNum;
                    data1.push(dataObj);
                    data3.push(el.key);
                    data4.push(el.disNum);
                });
            }

            if (bodyData.disAmtList && bodyData.disAmtList.length > 0) {
                bodyData.disAmtList.forEach(el => {
                    let dataObj = {};
                    dataObj['name'] = el.key;
                    dataObj['value'] = el.disAmt;
                    data2.push(dataObj);
                    data5.push(el.key);
                    data6.push(el.disAmt);
                });
            }
            switch (badnessType) {
                case 'CP':
                    if (data1 && data1.length != 0) {
                        data1.forEach(element => {
                            this.productList.forEach(element2 => {
                                if (element.name == element2.value) {
                                    element.name = element2.label;
                                }
                            });
                        });
                    }
                    if (data2 && data2.length != 0) {
                        data2.forEach(element => {
                            this.productList.forEach(element2 => {
                                if (element.name == element2.value) {
                                    element.name = element2.label;
                                }
                            });
                        });
                    }
                    console.log(data1);
                    this.option1 = {
                        title: {
                            text: '行业-新不良笔数',
                            left: 'center',
                            y: '10px',
                            textStyle: {
                                fontWeight: '500',
                                fontSize: '16'
                            }
                        },
                        series: [
                            {
                                data: data1,
                                type: 'pie',
                                radius: ['50%', '70%'],
                                label: {
                                    normal: {
                                        formatter: '{b} {c}'
                                    }
                                }
                            }
                        ]
                    }

                    this.option2 = {
                        title: {
                            text: '行业-新不良金额',
                            left: 'center',
                            y: '10px',
                            textStyle: {
                                fontWeight: '500',
                                fontSize: '16'
                            }
                        },
                        series: [
                            {
                                data: data2,
                                type: 'pie',
                                radius: ['50%', '70%'],
                                label: {
                                    normal: {
                                        formatter: '{b} {c}'
                                    }
                                }
                            }
                        ]
                    }
                    break;

                case 'HY':
                    if (data1 && data1.length != 0) {
                        data1.forEach(element => {
                            this.industryList.forEach(element2 => {
                                if (element.name == element2.value) {
                                    element.name = element2.label;
                                }
                            });
                        });
                    }
                    if (data2 && data2.length != 0) {
                        data2.forEach(element => {
                            this.industryList.forEach(element2 => {
                                if (element.name == element2.value) {
                                    element.name = element2.label;
                                }
                            });
                        });
                    }
                    this.option3 = {
                        title: {
                            text: '产品-新不良笔数',
                            left: 'center',
                            y: '10px',
                            textStyle: {
                                fontWeight: '500',
                                fontSize: '16'
                            }
                        },
                        series: [
                            {
                                data: data1,
                                type: 'pie',
                                radius: ['50%', '70%'],
                                label: {
                                    normal: {
                                        formatter: '{b} {c}'
                                    }
                                }
                            }
                        ]
                    }

                    this.option4 = {
                        title: {
                            text: '产品-新不良金额',
                            left: 'center',
                            y: '10px',
                            textStyle: {
                                fontWeight: '500',
                                fontSize: '16'
                            }
                        },
                        series: [
                            {
                                data: data2,
                                type: 'pie',
                                radius: ['50%', '70%'],
                                label: {
                                    normal: {
                                        formatter: '{b} {c}'
                                    }
                                }
                            }
                        ]
                    }
                    break;
                case 'DB':
                    if (data1 && data1.length != 0) {
                        data1.forEach(element => {
                            this.guaranteeList.forEach(element2 => {
                                if (element.name == element2.value) {
                                    element.name = element2.label;
                                }
                            });
                        });
                    }
                    if (data2 && data2.length != 0) {
                        data2.forEach(element => {
                            this.guaranteeList.forEach(element2 => {
                                if (element.name == element2.value) {
                                    element.name = element2.label;
                                }
                            });
                        });
                    }
                    this.option5 = {
                        title: {
                            text: '担保方式-新不良笔数',
                            left: 'center',
                            y: '10px',
                            textStyle: {
                                fontWeight: '500',
                                fontSize: '16'
                            }
                        },
                        series: [
                            {
                                data: data1,
                                type: 'pie',
                                radius: ['50%', '70%'],
                                label: {
                                    normal: {
                                        formatter: '{b} {c}'
                                    }
                                }
                            }
                        ]
                    }

                    this.option6 = {
                        title: {
                            text: '担保方式-新不良金额',
                            left: 'center',
                            y: '10px',
                            textStyle: {
                                fontWeight: '500',
                                fontSize: '16'
                            }
                        },
                        series: [
                            {
                                data: data2,
                                type: 'pie',
                                radius: ['50%', '70%'],
                                label: {
                                    normal: {
                                        formatter: '{b} {c}'
                                    }
                                }
                            }
                        ]
                    }
                    break;

                case 'NL':
                    if (data3 && data3.length != 0) {
                        data3.forEach((element, i) => {
                            this.ageList.forEach(element2 => {
                                if (element == element2.value) {
                                    data3[i] = element2.label;
                                }
                            });
                        });
                    }
                    if (data5 && data5.length != 0) {
                        data5.forEach((element, i) => {
                            this.ageList.forEach(element2 => {
                                if (element == element2.value) {
                                    data5[i] = element2.label;
                                }
                            });
                        });
                    }
                    this.option7 = {
                        title: {
                            text: '新不良笔数',
                            left: 'center',
                            y: '10px',
                            textStyle: {
                                fontWeight: 'normal'
                            }
                        },
                        xAxis: {
                            name: '年龄',
                            type: 'category',
                            data: data3,
                            // axisLine: {
                            //     show: true,
                            //     symbol: ['none', 'arrow'],
                            //     symbolSize: [8, 8],
                            //     symbolOfffset: [0, 7]
                            // },
                            axisTick: {
                                show: false
                            }
                        },
                        yAxis: {
                            name: '不良笔数',
                            type: 'value',
                            nameGap: 20,
                            scale: true,
                            splitArea: {
                                show: true
                            },
                            axisTick: {
                                show: false
                            },
                            axisLine: {
                                show: false
                            },
                        },
                        series: [
                            {
                                data: data4,
                                type: 'bar',
                                legendHoverLink: true,
                                barWidth: '40',
                                barCategoryGap: '20%',
                                itemStyle: {
                                    normal: {
                                        label: {
                                            show: true,
                                            position: 'top'
                                        }
                                    }
                                }
                            }
                        ]
                    };

                    this.option8 = {
                        title: {
                            text: '新不良金额',
                            left: 'center',
                            y: '10px',
                            textStyle: {
                                fontWeight: 'normal'
                            }
                        },
                        xAxis: {
                            name: '年龄',
                            type: 'category',
                            data: data5,
                            // axisLine: {
                            //     show: true,
                            //     symbol: ['none', 'arrow'],
                            //     symbolSize: [8, 8],
                            //     symbolOfffset: [0, 7]
                            // },
                            axisTick: {
                                show: false
                            }
                        },
                        yAxis: {
                            name: '不良金额（万元）',
                            type: 'value',
                            nameGap: 20,
                            scale: true,
                            splitArea: {
                                show: true
                            },
                            axisTick: {
                                show: false
                            },
                            axisLine: {
                                show: false
                            },
                        },
                        series: [
                            {
                                data: data6,
                                type: 'bar',
                                legendHoverLink: true,
                                barWidth: '40',
                                barCategoryGap: '20%',
                                itemStyle: {
                                    normal: {
                                        label: {
                                            show: true,
                                            position: 'top'
                                        }
                                    }
                                }
                            }
                        ]
                    }
                    break
                default:
                    break;
            }

        })
    }

}