import { Component, OnInit } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CustomerPerformanceHttpService } from 'app/pages/tzb/custom/http/customer-performance/customer-performance.http.service';
import { Message } from 'primeng/primeng';
import { CustomerPerformanceService } from '../../service/customer-performance.service';
import { ActivatedRoute } from '@angular/router';

@Component({
    selector: 'loan-growthGuide',
    templateUrl: './loan-growthGuide.component.html',
    styleUrls: ['./loan-growthGuide.component.scss'],
    providers: [Commfunc]
})

export class LoanGrowthGuideComponent implements OnInit {

    startDate: any = this.commfunc.transDateN(new Date().setTime(new Date().getTime() - 1000 * 60 * 60 * 24 * 30));
    endDate: any = this.commfunc.transDateN(new Date());
    commonHeader: any;
    msgs: Message[] = [];
    dmFlag: string;
    option1: any;
    option2: any;
    option3: any;
    option4: any;
    showIndex1: number = 1;
    showIndex2: number = 1;
    showIndex3: number = 1;
    showIndex4: number = 1;
    pTooltip1: string = `我：按日展示当日的贷款余额，按月展示自然月贷款日均\n同月入岗第一名：按日展示全行与我同月入岗的客户经理中最高的贷款余额，按月则日均同理\n同月入岗平均：按日展示全行与我同月入岗的客户经理合计贷款余额/同月入岗客户经理人数，按月则日均同理`;
    pTooltip2: string = `我：按日展示当日的贷款余额在同月入岗的客户经理合计贷款余额占比，按月展示自然月贷款日均在同月入岗的客户经理合计贷款日均占比\n同月入岗第一名：按日展示全行与我同月入岗的客户经理中最高的贷款余额在同月入岗的客户经理合计贷款余额占比，按月则日均同理\n同月入岗平均：1/同月入岗的客户经理数，计算同月入岗人均占比`;
    pTooltip3: string = `我：按日展示截止到每日在贷贷款客户数，按月展示自然月持有贷款客户数\n同月入岗第一名：按日展示全行与我同月入岗的客户经理中最多的在贷贷款客户数，按月同理\n同月入岗平均：按日展示全行与我同月入岗的客户经理合计在贷贷款客户数/同月入岗客户经理人数，按月同理`;

    constructor(
        public commfunc: Commfunc,
        public customerPerformanceHttpService: CustomerPerformanceHttpService,
        public customerPerformanceService: CustomerPerformanceService,
        public activatedRoute: ActivatedRoute,
    ) {
        this.commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        //初值
        this.option1 = this.customerPerformanceService.returnOption({ title: '同月入岗位贷款金额', legend: [{ name: '我' }, { name: '同月入岗第一名' }, { name: '同月入岗平均' }], xAxisData: [], yAxisName: '金额(万元)', series: [] });
        this.option2 = this.customerPerformanceService.returnOption({ title: '同月入岗贷款金额占比', legend: [{ name: '我' }, { name: '同月入岗第一名' }, { name: '同月入岗平均' }], xAxisData: [], yAxisName: '占比', series: [] });
        this.option3 = this.customerPerformanceService.returnOption({ title: '同月入岗贷款客户数', legend: [{ name: '我' }, { name: '同月入岗第一名' }, { name: '同月入岗平均' }], xAxisData: [], yAxisName: '客户数', series: [] });
    }

    ngOnInit() {
        let urlParams;
        this.activatedRoute.params.subscribe(params => {
            urlParams = params;
        });
        if (urlParams && JSON.stringify(urlParams) != '{}') {
            if (urlParams.flag != '1') {
                this.loansGrowingGuideAmountQuery(1, 1);
                this.loansGrowingGuideProportionQuery(2, 1);
                this.loansGrowingGuideCustQuery(3, 1);
            }
        }

    }

    outValue(event) {
        let objTemp;
        if (event.index == 1) {
            objTemp = this.customerPerformanceService.returnTime(event.queryCondition, event.index);
        } else if (event.index == 2) {
            objTemp = event.queryCondition;
        }
        this.startDate = objTemp.startDate;
        this.endDate = objTemp.endDate;
        console.log('startDate:', this.startDate);
        console.log('endDate:', this.endDate);
        this.loansGrowingGuideAmountQuery(1, 1);
        this.loansGrowingGuideProportionQuery(2, 1);
        this.loansGrowingGuideCustQuery(3, 1);
    }

    returnParams(ind, index) {
        this[`showIndex${ind}`] = index;
        if (index == 1) {
            this.dmFlag = 'D';
        } else {
            this.dmFlag = 'M';
        }
    }

    echoParams(ind, flag) {
        let index;
        if (flag == 'D') {
            index = 1;
        } else {
            index = 2;
        }
        this[`showIndex${ind}`] = index;
    }

    loansGrowingGuideAmountQuery(ind, index) {
        this.returnParams(ind, index);
        let params = {
            tellerId: this.commonHeader.userId,
            dmFlag: this.dmFlag,
            startDate: this.startDate,
            endDate: this.endDate
        }
        this.customerPerformanceHttpService.loansGrowingGuideAmountQuery(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let xData = [];
                let yData1 = [];
                let yData2 = [];
                let yData3 = [];
                if (data.myLoansAmountList && data.myLoansAmountList.length != 0 && data.togetherLoansAmountList && data.togetherLoansAmountList.length != 0 && data.togetherLoansAverageAmountList && data.togetherLoansAverageAmountList.length != 0) {
                    data.myLoansAmountList.forEach(element => {
                        let arr = element.dt.split('-');
                        if (data.dmFlag && data.dmFlag == 'D') {
                            xData.push(`${arr[0]}年${arr[1]}月${arr[2]}日`);
                        } else if (data.dmFlag && data.dmFlag == 'M') {
                            xData.push(`${arr[0]}年${arr[1]}月`);
                        }
                        yData1.push(element.dkje_w);
                    });
                    data.togetherLoansAmountList.forEach(element => {
                        yData2.push(element.dkje_dy);
                    });
                    data.togetherLoansAverageAmountList.forEach(element => {
                        yData3.push(element.dkje_pj);
                    });
                }
                this.echoParams(1, data.dmFlag);
                this.option1 = {
                    title: {
                        text: '同月入岗位贷款金额',
                        left: 'center',
                        y: '10px',
                        textStyle: {
                            fontWeight: '500',
                            fontSize: '16'
                        }
                    },
                    legend: {
                        data: [{ name: '我' }, { name: '同月入岗第一名' }, { name: '同月入岗平均' }],
                        top: 'bottom',
                        itemGap: 20,
                        icon: 'line',
                        textStyle: {
                            color: '#34CFE6'
                        }
                    },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'cross'
                        }
                    },
                    grid: {
                        width: '80%',
                        height: '75%',
                        left: '10%',
                        bottom: '10%',
                        containLabel: true
                    },
                    xAxis: {
                        type: 'category',
                        data: xData,
                        axisLabel: {
                            rotate: 30
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            show: false
                        }
                    },
                    yAxis: {
                        type: 'value',
                        name: '金额(万元)',
                        nameGap: 30,
                        scale: true,
                        splitArea: {
                            show: true
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            show: false
                        }
                    },
                    series: [
                        {
                            name: '我',
                            data: yData1,
                            type: 'line',
                            itemStyle: {
                                normal: {
                                    label: {
                                        show: true,
                                        formatter: (params) => {
                                            if (params.dataIndex == 0 || params.dataIndex == yData1.length - 1) {
                                                return params.value
                                            } else {
                                                return ''
                                            }
                                        }
                                    }
                                }
                            }
                        },
                        {
                            name: '同月入岗第一名',
                            data: yData2,
                            type: 'line',
                            itemStyle: {
                                normal: {
                                    lineStyle: {
                                        type: 'dashed'
                                    },
                                    label: {
                                        show: true,
                                        formatter: (params) => {
                                            if (params.dataIndex == 0 || params.dataIndex == yData2.length - 1) {
                                                return params.value
                                            } else {
                                                return ''
                                            }
                                        }
                                    }
                                }
                            }
                        },
                        {
                            name: '同月入岗平均',
                            data: yData3,
                            type: 'line',
                            itemStyle: {
                                normal: {
                                    lineStyle: {
                                        type: 'dashed'
                                    },
                                    label: {
                                        show: true,
                                        formatter: (params) => {
                                            if (params.dataIndex == 0 || params.dataIndex == yData2.length - 1) {
                                                return params.value
                                            } else {
                                                return ''
                                            }
                                        }
                                    }
                                }
                            }
                        },
                    ]
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }

    loansGrowingGuideProportionQuery(ind, index) {
        this.returnParams(ind, index);
        let params = {
            tellerId: this.commonHeader.userId,
            dmFlag: this.dmFlag,
            startDate: this.startDate,
            endDate: this.endDate
        }
        this.customerPerformanceHttpService.loansGrowingGuideProportionQuery(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let xData = [];
                let yData1 = [];
                let yData2 = [];
                let yData3 = [];
                if (data.myLoansAmountPTList && data.myLoansAmountPTList.length != 0 && data.togetherLoansAmountPTList && data.togetherLoansAmountPTList.length != 0 && data.togetherLoansAverageAmountPTList && data.togetherLoansAverageAmountPTList.length != 0) {
                    data.myLoansAmountPTList.forEach(element => {
                        let arr = element.dt.split('-');
                        if (data.dmFlag && data.dmFlag == 'D') {
                            xData.push(`${arr[0]}年${arr[1]}月${arr[2]}日`);
                        } else if (data.dmFlag && data.dmFlag == 'M') {
                            xData.push(`${arr[0]}年${arr[1]}月`);
                        }
                        yData1.push(Number(element.dkjezh_w * 100).toFixed(2));
                    });
                    data.togetherLoansAmountPTList.forEach(element => {
                        yData2.push(Number(element.dkjezh_dy * 100).toFixed(2));
                    });
                    data.togetherLoansAverageAmountPTList.forEach(element => {
                        yData3.push(Number(element.dkjezh_pj * 100).toFixed(2));
                    });
                }
                this.echoParams(2, data.dmFlag);
                this.option2 = {
                    title: {
                        text: '同月入岗贷款金额占比',
                        left: 'center',
                        y: '10px',
                        textStyle: {
                            fontWeight: '500',
                            fontSize: '16'
                        }
                    },
                    legend: {
                        data: [{ name: '我' }, { name: '同月入岗第一名' }, { name: '同月入岗平均' }],
                        top: 'bottom',
                        itemGap: 20,
                        icon: 'line',
                        textStyle: {
                            color: '#34CFE6'
                        }
                    },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'cross'
                        }
                    },
                    grid: {
                        width: '80%',
                        height: '75%',
                        left: '10%',
                        bottom: '10%',
                        containLabel: true
                    },
                    xAxis: {
                        type: 'category',
                        data: xData,
                        axisLabel: {
                            rotate: 30
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            show: false
                        }
                    },
                    yAxis: {
                        type: 'value',
                        name: '占比',
                        nameGap: 30,
                        scale: true,
                        axisLabel: {
                            formatter: '{value}%'
                        },
                        splitArea: {
                            show: true
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            show: false
                        }
                    },
                    series: [
                        {
                            name: '我',
                            data: yData1,
                            type: 'line',
                            itemStyle: {
                                normal: {
                                    label: {
                                        show: true,
                                        formatter: (params) => {
                                            if (params.dataIndex == 0 || params.dataIndex == yData1.length - 1) {
                                                return params.value
                                            } else {
                                                return ''
                                            }
                                        }
                                    }
                                }
                            }
                        },
                        {
                            name: '同月入岗第一名',
                            data: yData2,
                            type: 'line',
                            itemStyle: {
                                normal: {
                                    lineStyle: {
                                        type: 'dashed'
                                    },
                                    label: {
                                        show: true,
                                        formatter: (params) => {
                                            if (params.dataIndex == 0 || params.dataIndex == yData2.length - 1) {
                                                return params.value
                                            } else {
                                                return ''
                                            }
                                        }
                                    }
                                }
                            }
                        },
                        {
                            name: '同月入岗平均',
                            data: yData3,
                            type: 'line',
                            itemStyle: {
                                normal: {
                                    lineStyle: {
                                        type: 'dashed'
                                    },
                                    label: {
                                        show: true,
                                        formatter: (params) => {
                                            if (params.dataIndex == 0 || params.dataIndex == yData3.length - 1) {
                                                return params.value
                                            } else {
                                                return ''
                                            }
                                        }
                                    }
                                }
                            }
                        },
                    ]
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }

    loansGrowingGuideCustQuery(ind, index) {
        this.returnParams(ind, index);
        let params = {
            tellerId: this.commonHeader.userId,
            dmFlag: this.dmFlag,
            startDate: this.startDate,
            endDate: this.endDate
        }
        this.customerPerformanceHttpService.loansGrowingGuideCustQuery(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let xData = [];
                let yData1 = [];
                let yData2 = [];
                let yData3 = [];
                if (data.myLoansCustList && data.myLoansCustList.length != 0 && data.togetherLoansCustList && data.togetherLoansCustList.length != 0 && data.togetherLoansAverageCustList && data.togetherLoansAverageCustList.length != 0) {
                    data.myLoansCustList.forEach(element => {
                        let arr = element.dt.split('-');
                        if (data.dmFlag && data.dmFlag == 'D') {
                            xData.push(`${arr[0]}年${arr[1]}月${arr[2]}日`);
                        } else if (data.dmFlag && data.dmFlag == 'M') {
                            xData.push(`${arr[0]}年${arr[1]}月`);
                        }
                        yData1.push(element.dkkh_w);
                    });
                    data.togetherLoansCustList.forEach(element => {
                        yData2.push(element.dkkh_dy);
                    });
                    data.togetherLoansAverageCustList.forEach(element => {
                        yData3.push(element.dkkh_pj);
                    });
                }
                this.echoParams(3, data.dmFlag);
                this.option3 = {
                    title: {
                        text: '同月入岗贷款客户数',
                        left: 'center',
                        y: '10px',
                        textStyle: {
                            fontWeight: '500',
                            fontSize: '16'
                        }
                    },
                    legend: {
                        data: [{ name: '我' }, { name: '同月入岗第一名' }, { name: '同月入岗平均' }],
                        top: 'bottom',
                        itemGap: 20,
                        icon: 'line',
                        textStyle: {
                            color: '#34CFE6'
                        }
                    },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'cross'
                        }
                    },
                    grid: {
                        width: '80%',
                        height: '75%',
                        left: '10%',
                        bottom: '10%',
                        containLabel: true
                    },
                    xAxis: {
                        type: 'category',
                        data: xData,
                        axisLabel: {
                            rotate: 30
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            show: false
                        }
                    },
                    yAxis: {
                        type: 'value',
                        name:'客户数',
                        nameGap: 30,
                        scale: true,
                        splitArea: {
                            show: true
                        },
                        axisTick: {
                            show: false
                        },
                        axisLine: {
                            show: false
                        }
                    },
                    series: [
                        {
                            name: '我',
                            data: yData1,
                            type: 'line',
                            itemStyle: {
                                normal: {
                                    label: {
                                        show: true,
                                        formatter: (params) => {
                                            if (params.dataIndex == 0 || params.dataIndex == yData1.length - 1) {
                                                return params.value
                                            } else {
                                                return ''
                                            }
                                        }
                                    }
                                }
                            }
                        },
                        {
                            name: '同月入岗第一名',
                            data: yData2,
                            type: 'line',
                            itemStyle: {
                                normal: {
                                    lineStyle: {
                                        type: 'dashed'
                                    },
                                    label: {
                                        show: true,
                                        formatter: (params) => {
                                            if (params.dataIndex == 0 || params.dataIndex == yData2.length - 1) {
                                                return params.value
                                            } else {
                                                return ''
                                            }
                                        }
                                    }
                                }
                            }
                        },
                        {
                            name: '同月入岗平均',
                            data: yData3,
                            type: 'line',
                            itemStyle: {
                                normal: {
                                    lineStyle: {
                                        type: 'dashed'
                                    },
                                    label: {
                                        show: true,
                                        formatter: (params) => {
                                            if (params.dataIndex == 0 || params.dataIndex == yData3.length - 1) {
                                                return params.value
                                            } else {
                                                return ''
                                            }
                                        }
                                    }
                                }
                            }
                        },
                    ]
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
}